import { createSlice } from '@reduxjs/toolkit'
import { now } from 'lodash'
import getOGConfig from 'sdk/nft/og'
import { nftOnSellPageQCState } from '../types'

const ogConfig = getOGConfig()

const initialState: nftOnSellPageQCState = 
{
  allQuery: 
  {
    version: now(),

    nft: ogConfig.address,

    page: 1,

    pagesize: 20,

    desc: true,

    paytoken: '*',

    selectedPayToken: {
      label: 'All',

      value: '*'
    }
  }
}

const nftOnSellPageQCSlice = createSlice({
    name: 'nftOnSellPageQC',
  
    initialState,

    reducers: {
      updateSelectedPayToken:  (state, action) => {
          const info = action.payload

          const _qc = state.allQuery

          _qc.version = now()

          _qc.paytoken = info.value

          _qc.selectedPayToken = info
      },

      updateDesc: (state, action) => {
        const info = action.payload

        const _qc = state.allQuery

        _qc.version = now()

        _qc.desc = info
    },

    updateVersion: (state) => {
      const _qc = state.allQuery

      _qc.version = now()
      },
    }
})

// Actions
export const { updateSelectedPayToken, updateDesc, updateVersion } = nftOnSellPageQCSlice.actions

export default nftOnSellPageQCSlice.reducer

export const setOnSellPageQCSelectedPayToken = (_selectedPayToken) => async (dispatch) =>{
  dispatch(updateSelectedPayToken(_selectedPayToken))
}

export const setOnSellPageQCDesc = (_desc: boolean) => async (dispatch) => {
  dispatch(updateDesc(_desc))
}

export const setOnSellPageQCVersion = () => async (dispatch) => {
  dispatch(updateVersion())
}
   

  