import React, { useState } from 'react'
import { Button, Modal, Flex, Text } from 'uikit'
import { useTranslation } from 'contexts/Localization'
import useNftSellCancelAsk from 'hooks/useNftSellCancelAsk'
 
interface NftOnSellCancelAskModalProps {
  shop: string,

  tokenid: string,

  onDismiss?: () => void,

  onSuccess?: () => void
}

const NftOnSellCancelAskModal: React.FC<NftOnSellCancelAskModalProps> = ({ shop, tokenid, onDismiss, onSuccess }) => {
  const [pendingTx, setPendingTx] = useState(false)

  const [commitResult, setCommitResult] = useState({ step: 'step 1', message: 'waiting for commit', success: true })

  const { t } = useTranslation()

  const cancelAskCallback = () =>
  {
    setPendingTx(false)

    setCommitResult({
      success: true,

      step: "end",

      message: "success"
    })

    if(onSuccess)
    {
      onSuccess()
    }

    onDismiss()
  }

  const [ onCancelAsk ] = useNftSellCancelAsk(shop, tokenid, cancelAskCallback)

  const handleOnCancelAsk = async () => {
      try {
        setPendingTx(true)

        await onCancelAsk()
      } catch (e) {

        setPendingTx(false)

        setCommitResult({
          success: false,
    
          step: "step 1",
    
          message: e.message
        }) 
      }
  }

  return (
    <Modal title={t('Commit')} onDismiss={onDismiss} >
      <Flex maxWidth='420px' minWidth='420px' alignItems="center" flexDirection="column" justifyContent="space-between" mb="8px">
        <Text bold color="textSubtle" fontSize="18px" pr="3px">
         {t(commitResult.step)}
        </Text>
        { commitResult.success ? (<Text color="textSubtle" fontSize="14px" pr="3px" mt='10px'>
         {t(commitResult.message)}
        </Text>) :
        (<Text bold color="failure" fontSize="14px" pr="3px" mt='10px'>
         {t(commitResult.message)} 
        </Text> )}
      </Flex>

      <Button
        isLoading={pendingTx}
        onClick={handleOnCancelAsk}
        mt="24px"
      >
        {pendingTx ? t('Committing') : t('Commit')}
      </Button>
    </Modal>
  )
}

export default NftOnSellCancelAskModal
