/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit'
import getLiquiditypools from 'sdk/liquiditypools'
import fetchFarms from './fetchFarms'
import {
  fetchFarmUserEarnings,
  fetchFarmUserAllowances,
  fetchFarmUserTokenBalances,
  fetchFarmUserStakedBalances,
  fetchFarmUserAllowanceById,
  fetchUserDataById
} from './fetchFarmUser'
import { LiquidityPoolState, LiquidityPool } from '../types'

const noAccountFarmConfig = getLiquiditypools().map((lp) => ({
  ...lp,
  userData: {
    allowance: '0',
    tokenBalance: '0',
    stakedBalance: '0',
    earnings: '0',
  },
}))

const initialState: LiquidityPoolState = { data: noAccountFarmConfig, userDataLoaded: false }

export const LiquiditypoolsSlice = createSlice({
  name: 'Liquiditypools',

  initialState,
  
  reducers: {
    setFarmsPublicData: (state, action) => {
      const liveFarmsData: LiquidityPool[] = action.payload
      state.data = state.data.map((farm) => {
        const liveFarmData = liveFarmsData.find((f) => f.liquidityid === farm.liquidityid)
        return { ...farm, ...liveFarmData }
      })
    },

    setFarmUserData: (state, action) => {
      const { arrayOfUserDataObjects } = action.payload

      arrayOfUserDataObjects.forEach((value) => {
        const { liquidityid } = value
        
        const index = state.data.findIndex((farm) => farm.liquidityid === liquidityid)

        state.data[index] = { ...state.data[index], userData: value }
      })
      state.userDataLoaded = true
    },

    updateUserData: (state, action) => {
      const { lid, tokenBalance, stakedBalance, earnings } = action.payload

      const node = state.data.find((farm) => farm.liquidityid === lid)

      if(node && node.userData)
      {
        node.userData.stakedBalance = stakedBalance

        node.userData.tokenBalance = tokenBalance

        node.userData.earnings = earnings
      }
    },

    updateUserAllowance: (state, action) => {
      const { lid, allowance } = action.payload

      const node = state.data.find((farm) => farm.liquidityid === lid)

      if(node && node.userData)
      {
        node.userData.allowance = allowance
      }
    },
  },
})

// Actions
export const { setFarmsPublicData, setFarmUserData, updateUserAllowance, updateUserData } = LiquiditypoolsSlice.actions

// Thunks
export const fetchFarmsPublicDataAsync = () => async (dispatch) => {
  const farmsToFetch =  getLiquiditypools()  

  const farms = await fetchFarms(farmsToFetch)
  
  dispatch(setFarmsPublicData(farms))
}

export const fetchFarmUserDataAsync = (account: string) => async (dispatch) => {
  const farmsToFetch =  getLiquiditypools()  

  const userFarmAllowances = await fetchFarmUserAllowances(account, farmsToFetch)

  const userFarmTokenBalances = await fetchFarmUserTokenBalances(account, farmsToFetch)

  const userStakedBalances = await fetchFarmUserStakedBalances(account, farmsToFetch)

  const userFarmEarnings = await fetchFarmUserEarnings(account, farmsToFetch)

  const arrayOfUserDataObjects = userFarmAllowances.map((farmAllowance, index) => {
    return {
      liquidityid: farmsToFetch[index].liquidityid,

      allowance: userFarmAllowances[index],

      tokenBalance: userFarmTokenBalances[index],

      stakedBalance: userStakedBalances[index],

      earnings: userFarmEarnings[index],
    }
  })

  dispatch(setFarmUserData({ arrayOfUserDataObjects }))
}

export const updateFarmUserAllowance = (liquidityid: any, account: string) => async (dispatch) => {
  const result = fetchFarmUserAllowanceById(liquidityid, account)

  const info = {
    lid: liquidityid,

    allowance: result,
  }

  dispatch(updateUserAllowance(info))
}

export const updateUserDataAsync = (liquidityid: string, account: string) => async (dispatch) => {
  const result = await fetchUserDataById(liquidityid, account)

  const info = {
    lid: liquidityid,

    tokenBalance: result.stakingTokenBalance,

    stakedBalance: result.stakedBalance,

    earnings: result.pendingReward
  }

  dispatch(updateUserData(info))
}

export default LiquiditypoolsSlice.reducer
