export const onPriceAmountKeyDown = (e: React.KeyboardEvent<HTMLInputElement>, decimals?: number) =>
{
  const _key = e.key

  if(_key === 'Backspace')
  {
    return
  }

  const regex = RegExp('[0-9.]')

  if(!regex.test(_key))
  {
    e.preventDefault()

    return
  }
   
   const _value = (e.target as HTMLInputElement)?.value

    if(_key === '.')
    {
      if(_value && _value.indexOf('.') >= 0)
      {
        e.preventDefault()
      }
    }

    if(_value.length > (MaxAmountLength - (decimals || 0)))
    {
      e.preventDefault()
    }
}

export const MaxAmountLength = 29