import BigNumber from 'bignumber.js'
import erc20ABI from 'sdk/abis/erc20.json'
import liquiditypoolABI from 'sdk/abis/liquiditypool.json'
import multicall from 'utils/multicall'
import { BIG_TEN, LiquidityPoolConfig, DEFAULT_TOKEN_DECIMAL } from 'sdk'
 
const fetchFarms = async (farmsToFetch: LiquidityPoolConfig[]) => {
  const data = await Promise.all(
    farmsToFetch.map(async (liquiditypool) => {
      const lpAddress = liquiditypool.liquidity.address

      const calls = [
        // Balance of token in the LP contract
        {
          address: liquiditypool.liquidity.token.address,

          name: 'balanceOf',
          
          params: [lpAddress],
        },

        // Balance of quote token on LP contract
        {
          address: liquiditypool.liquidity.quoteToken.address,

          name: 'balanceOf',
          
          params: [lpAddress],
        },

        // Total supply of LP tokens
        {
          address: lpAddress,

          name: 'totalSupply',
        },
      ]

      const poolCalls = [
        {
          address: liquiditypool.contractAddress,
    
          name: 'totalStaked'
        },

        {
          address: liquiditypool.contractAddress,
    
          name: 'startBlock',
        },

        {
          address: liquiditypool.contractAddress,
    
          name: 'bonusEndBlock',
        }
      ]

      const [
        tokenBalanceLP,

        quoteTokenBalanceLP,

        lpTotalSupply,
      ] = await multicall(erc20ABI, calls)

      const [
        totalStaked,

        startBlock,

        endBlock,
      ] = await multicall(liquiditypoolABI, poolCalls)

      // Ratio in % a LP tokens that are in staking, vs the total number in circulation
      const lpTokenRatio = new BigNumber(totalStaked).div(new BigNumber(lpTotalSupply))

      // Total value in staking in quote token value
      const lpTotalInQuoteToken = new BigNumber(quoteTokenBalanceLP)
        .div(BIG_TEN.pow(liquiditypool.liquidity.quoteToken.decimals) || DEFAULT_TOKEN_DECIMAL)
        .times(new BigNumber(2))
        .times(lpTokenRatio)

      // Amount of token in the LP that are considered staking (i.e amount of token * lp ratio)
      const tokenAmount = new BigNumber(tokenBalanceLP).div(BIG_TEN.pow(liquiditypool.liquidity.token.decimals)).times(lpTokenRatio)

      const quoteTokenAmount = new BigNumber(quoteTokenBalanceLP)
        .div(BIG_TEN.pow(liquiditypool.liquidity.quoteToken.decimals) || DEFAULT_TOKEN_DECIMAL)
        .times(lpTokenRatio)

      return {
        ...liquiditypool,
        tokenAmount: tokenAmount.toJSON(),

        quoteTokenAmount: quoteTokenAmount.toJSON(),

        lpTotalSupply: new BigNumber(lpTotalSupply).toJSON(),

        lpTotalInQuoteToken: lpTotalInQuoteToken.toJSON(),

        tokenPriceVsQuote: quoteTokenAmount.div(tokenAmount).toJSON(),

        startBlock: new BigNumber(startBlock).toJSON(),

        endBlock: new BigNumber(endBlock).toJSON(),
      }
    }),
  )

  return data
}

export default fetchFarms
