import { ChainId, getENVChainId } from '../constants'
import MULTICALL_ABI from './abi.json'

const MULTICALL_NETWORKS: { [chainId in ChainId]: string } = {
  [ChainId.BSCNET]: '0x1Ee38d535d541c55C9dae27B12edf090C608E6Fb', // TODO

  [ChainId.BSCTESTNET]: '0x301907b5835a2d723Fe3e9E8C5Bc5375d5c1236A',

  [ChainId.HTNET]: '0x2aE32f139e5300B7f89b4971b2Ab4fd5A3a41d94',

  [ChainId.HSCNET]: '0x127e3C89D929Ee71788a8e4D90cC8fbDAff107A2'
}

const getMulticallAddress = (chainId?: ChainId) =>
{
   return chainId ? MULTICALL_NETWORKS[chainId] : MULTICALL_NETWORKS[getENVChainId()]
}

export { MULTICALL_ABI, getMulticallAddress }
