import React from 'react'
import { Flex, Text } from 'uikit'
import styled from 'styled-components'
import { useTranslation } from 'contexts/Localization'

const Wrapper = styled.div`
  margin-top: 10px;
  margin-bottom: 10px;
`

interface AirdropInfoRowProps {
  airdropCount: number, 
  baseAirdropAmount: number, 
}

const AirdropInfoRow: React.FC<AirdropInfoRowProps> = ({
  airdropCount = 0, 
  baseAirdropAmount = 0, 
}) => {
  const { t } = useTranslation()

  const dropSupply = baseAirdropAmount * airdropCount

  return (
    <Wrapper>
      <Flex justifyContent="space-between" mb='5px'>
        <Text ml='5px'>{t('AirdropSupply')}:</Text>
        <Text mr='5px'>{dropSupply}</Text>
      </Flex>
      <Flex justifyContent="space-between" mb='5px'>
        <Text ml='5px'>{t('AirdropCounts')}:</Text>
        <Text mr='5px'>{airdropCount}</Text>
      </Flex>
    </Wrapper>
  )
}

export default AirdropInfoRow
