import React from 'react'
import { useActiveWeb3React } from 'hooks'
import { useFetchAirdropData } from 'state/hooks'
import Airdroppools from './Airdroppools'

const Airdroppoolsagent : React.FC = () => {
    const { account } = useActiveWeb3React()

    useFetchAirdropData(account)

    return (
     <>
       <Airdroppools />
     </>
    );
}

export default Airdroppoolsagent