import { ChainId, getENVChainId } from './constants'

// rpc url
const rpcurls: { [chainId in ChainId]: string }  = 
{
   [ChainId.HTNET]: process.env.REACT_APP_NODE_ht,

   [ChainId.BSCNET]: process.env.REACT_APP_NODE_bsc,

   [ChainId.HSCNET]: process.env.REACT_APP_NODE_hsc,

   [ChainId.BSCTESTNET]: ''
}

export const getNodeUrl = (chainId?: ChainId) => {
  return chainId ? rpcurls[chainId] : rpcurls[getENVChainId()]
}

// scan link
const SCAN_BASEURLS: { [chainId in ChainId]: string } = 
{
   [ChainId.BSCNET]: 'https://bscscan.com',

   [ChainId.BSCTESTNET]: 'https://testnet.bscscan.com',

   [ChainId.HTNET]: 'https://hecoinfo.com',

   [ChainId.HSCNET]: 'https://hooscan.com/',
}

export function getScanLink(chainId: ChainId, data: string, type: 'transaction' | 'token' | 'address'): string {
   const prefix = SCAN_BASEURLS[chainId]
 
   switch (type) {
     case 'transaction': {
       return `${prefix}/tx/${data}`
     }

     case 'token': {
       return `${prefix}/token/${data}`
     }

     case 'address':
     default: {
       return `${prefix}/address/${data}`
     }
   }
}
