import bscList from './56.json'
import htList from './128.json'
import hscList from './70.json'
import { ChainId, getENVChainId } from '../constants'

const tokenLists = 
{
   [ChainId.HTNET]: htList,

   [ChainId.BSCNET]: bscList,

   [ChainId.HSCNET]: hscList
}

export default function getTokenLists(chainId? : ChainId)
{
   return chainId ? tokenLists[chainId] : tokenLists[getENVChainId()]
}