import { useEffect } from 'react'
import BigNumber from 'bignumber.js'
import { useSelector } from 'react-redux'
import { getENVChainId, BIG_ZERO } from 'sdk'
import useRefresh from 'hooks/useRefresh' 
import getWeb3NoAccount from 'utils/web3'
import { useAppDispatch } from 'state'
import { 
  State, 
  PriceState, 
  SyrupPool, 
  LiquidityPoolState, 
  LiquidityPool, 
  AirdropPool, 
  nftForSellNode,
  nftMetaModuleNode,
  nftOnSellPageModule,
  nftOnSellBuyNode,
  nftOnSellPageQueryConditions,
  nftOnSellNode } from './types'
import { 
  fetchFarmsPublicDataAsync, 
  fetchAirdropsDataAsync, 
  fetchPoolsUserDataAsync, 
  fetchPoolsPublicDataAsync, 
  fetchPricesAsync,
  setBlock, 
  fetchAirdropsUserDataAsync,
  fetchOGAsync,
  fetchOGUserAsync } from './actions'
 
export const useFetchPublicData = () => {
  const dispatch = useAppDispatch()

  const chainId = getENVChainId()

  const { fastRefresh, slowRefresh } = useRefresh()

  useEffect(() => {
    dispatch(fetchPricesAsync())
  }, [dispatch, fastRefresh])

  useEffect(() => {
    dispatch(fetchFarmsPublicDataAsync())

    dispatch(fetchPoolsPublicDataAsync())

    dispatch(fetchAirdropsDataAsync())
  }, [dispatch, slowRefresh])

  useEffect(() => {
    const web3 = getWeb3NoAccount(chainId)

    const interval = setInterval(async () => {
      const blockNumber = await web3.eth.getBlockNumber()

      dispatch(setBlock(blockNumber))
    }, 6000)

    return () => clearInterval(interval)
  }, [dispatch, chainId])
}

export const useFetchAirdropData = (account) => {
  const dispatch = useAppDispatch()

  const { slowRefresh } = useRefresh()

  useEffect(() => {
    dispatch(fetchAirdropsDataAsync())

    if(account)
    {
      dispatch(fetchAirdropsUserDataAsync(account))
    }
  }, [dispatch, account, slowRefresh])
}

export const useGetApiPrices = () => {
  const prices: PriceState['data'] = useSelector((state: State) => state.prices.data)

  return prices
}

export const useGetApiPrice = (address: string) => {
  const prices = useGetApiPrices()

  if (!prices) {
    return null
  }

  return prices[address]
}

// Block
export const useBlock = () => {
  return useSelector((state: State) => state.block)
}

export const useInitialBlock = () => {
  return useSelector((state: State) => state.block.initialBlock)
}
 
export const useSyrupPools = (account): SyrupPool[] => {
  const { fastRefresh } = useRefresh()

  const dispatch = useAppDispatch()

  useEffect(() => {
    if (account) {
      dispatch(fetchPoolsUserDataAsync(account))
    }
  }, [account, dispatch, fastRefresh])

  const pools = useSelector((state: State) => state.syruppools.data)

  return pools
}

export const usePoolFromId = (syrupId): SyrupPool => {
  const pool = useSelector((state: State) => state.syruppools.data.find((p) => p.syrupId === syrupId))

  return pool
}

export const useLiquidityPools = (): LiquidityPoolState => {
  const farms = useSelector((state: State) => state.liquiditypools)

  return farms
}

export const useFarmFromId = (liquidityid): LiquidityPool => {
  const farm = useSelector((state: State) => state.liquiditypools.data.find((f) => f.liquidityid === liquidityid))

  return farm
}

export const useFarmFromSymbol = (lpSymbol: string): LiquidityPool => {
  const farm = useSelector((state: State) => state.liquiditypools.data.find((f) => f.liquidity.symbol === lpSymbol))
 
  return farm
}

export const useFarmUser = (liquidityid) => {
  const farm = useFarmFromId(liquidityid)

  return {
    allowance: farm.userData ? new BigNumber(farm.userData.allowance) : BIG_ZERO,

    tokenBalance: farm.userData ? new BigNumber(farm.userData.tokenBalance) : BIG_ZERO,

    stakedBalance: farm.userData ? new BigNumber(farm.userData.stakedBalance) : BIG_ZERO,

    earnings: farm.userData ? new BigNumber(farm.userData.earnings) : BIG_ZERO,
  }
}

export const useAirdropPools = (account): AirdropPool[] => {
  const { fastRefresh } = useRefresh()

  const dispatch = useAppDispatch()

  useEffect(() => {
   if (account) {
     dispatch(fetchAirdropsUserDataAsync(account))
   }
  }, [account, dispatch, fastRefresh])

  const airdrops = useSelector((state: State) => state.airdroppools.data)

  return airdrops.filter((p) => p.info && p.info.airdropCount > 0)
}
 
export const useOG= () => {
  return useSelector((state: State) => state.og.data)
}

export const useFetchOG = (account) => {
  const dispatch = useAppDispatch()

  const { slowRefresh } = useRefresh()

  useEffect(() => {
    dispatch(fetchOGAsync())

    if(account)
    {
      dispatch(fetchOGUserAsync(account))
    }
  }, [dispatch, account, slowRefresh])
}

export const useNftForSell = (nft: string, tokenid: string) : nftForSellNode =>
{
  const value = useSelector((state: State) => state.nftForSell.data.find((p) => p.nft === nft))

  return value?.forsells?.get(tokenid)
}

export const useNftOnSellBuy = (nft: string, tokenid: string) : nftOnSellBuyNode =>
{
  const value = useSelector((state: State) => state.nftOnSellBuys.data.find((p) => p.nft === nft))

  return value?.onbuys?.get(tokenid)
}

export const useNftMeta = (nft: string, tokenid: string) : nftMetaModuleNode =>
{
  const value = useSelector((state: State) => state.nftMetas.data.find((p) => p.nft === nft))

  return value?.metas?.get(tokenid)
}

export const useNftOnsells = (pagevision: number) : nftOnSellPageModule =>
{
  const value = useSelector((state: State) => state.nftOnSells.data.get(pagevision))

  return value
}

export const useNftOnSellPreview = (nft: string, tokenid: string) : nftOnSellNode =>
{
  const qc = useNftOnsellPageQC()

  const value = useSelector((state: State) => state.nftOnSells.data.get(qc.version))

  if(value?.module?.nft === nft)
  {
    return value.module.asks.find((ask) => ask.tokenid === tokenid)
  }

  const key = `${nft}@${tokenid}`

  return value?.hashasks?.get(key)
}

export const useNftOnsellPageQC = () : nftOnSellPageQueryConditions =>
{
  const value = useSelector((state: State) => state.nftOnSellPageQC.allQuery)

  return value
}