import mdex_router_ABI from './mdexrouter.json'
import uni_router_abi from './IUniswapV2Router02.json'
import { ChainId, getENVChainId } from '../../constants'

const routerABIs = 
{
   [ChainId.HTNET]: mdex_router_ABI,

   [ChainId.BSCNET]: uni_router_abi,

   [ChainId.HSCNET]: uni_router_abi
}

export default function getRouterABI(chainId? : ChainId)
{
   return chainId ? routerABIs[chainId] : routerABIs[getENVChainId()]
}

 