import BigNumber from 'bignumber.js'
import getAirdrops from 'sdk/airdrops'
import airdropABI from 'sdk/abis/airdrop.json'
import multicall from 'utils/multicall'
import { getBalanceNumber } from 'utils/formatBalance'

const fetchAirdropsUser  = async (account) => {
  const calls = getAirdrops().map((p) => ({
    address: p.address,

    name: 'getUserSummary',

    params: [account],
  }))

  const results = await multicall(airdropABI, calls)

  return getAirdrops().map((p, index) => {
    const result = results[index]

    return {
      id: p.id,

      userAirdropAmount: getBalanceNumber(new BigNumber(result[0].toString()), p.rewardToken.decimals), 

      userInviterAmount: getBalanceNumber(new BigNumber(result[1].toString()), p.rewardToken.decimals), 
      
      userInviterCount: result[2].toString(), 
    }
  })
}

export default fetchAirdropsUser