import BigNumber from 'bignumber.js'
import getAirdrops from 'sdk/airdrops'
import airdropABI from 'sdk/abis/airdrop.json'
import { getBalanceNumber } from 'utils/formatBalance'
import multicall from 'utils/multicall'

const fetchAirdrops = async () => {
  const airdrops = getAirdrops()
   
  const calls = airdrops.map((p) => {
    return {
      address: p.address,

      name: 'getSummary',
    }
  })

  const results = await multicall(airdropABI, calls)

  const infoes = airdrops.map((p, index) => {
    const result = results[index]

    return {
      id: p.id,

      airdropCount: result[0].toString(),

      hasAirdropCount: result[1].toString(),

      baseAirdropAmount: getBalanceNumber(new BigNumber(result[2].toString()), p.rewardToken.decimals),

      sgeOffset: result[3].toString(),

      inviteRewardRatio: result[4].toString(),

      hasAirdropAmount: getBalanceNumber(new BigNumber(result[5].toString()), p.rewardToken.decimals),
    }
  })

  return infoes
}

export default fetchAirdrops

 
