/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit'
import { getNftMarket } from 'sdk/nft/shop'
import { fetchAllowance, fetchFeePercent } from './fetchNftForSellUser'
import { nftForSellNode, nftForSell, nftForSellState } from '../types'

const { shops }= getNftMarket()

const initialState: nftForSellState = { data: shops.map((shop)=>
 { 
   const item = { 
    nft: shop.nft,

    forsells: new Map<string, nftForSellNode>()
   } as nftForSell

   return item
 })}

export const nftForSellsSlice = createSlice({
  name: 'nftForSells',

  initialState,
  
  reducers: {
    setLoading:  (state, action) => {
      const info = action.payload

      const node = state.data.find((item) => item.nft === info.nftKey)

      if(node)
      {
        const sellNodeIn = node.forsells.get(info.nftId)

        if(sellNodeIn)
        {
          sellNodeIn.loading = true
        }

        else 
        {
          const sellNode = 
          {
            loading: true,
  
            isApproved: false,
  
            feePercent: 0,

            owner: undefined
          }

          node.forsells.set(info.nftId, sellNode)
        }
      }
    },

    setNftForSell: (state, action) => {
      const info = action.payload

      const node = state.data.find((item) => item.nft === info.nftKey)

      if(node)
      {
        const sellNodeIn = node.forsells.get(info.nftId)

        if(sellNodeIn)
        {
          sellNodeIn.loading = false

          sellNodeIn.isApproved = info.isApproved

          sellNodeIn.feePercent = info.feePercent

          sellNodeIn.owner = info.owner
        }

        else 
        {
          const sellNode = 
          {
            loading: false,
  
            isApproved: info.isApproved,
  
            feePercent: info.feePercent,

            owner: info.owner
          }
  
          node.forsells.set(info.nftId, sellNode)
        }
      }
    }
  }
})

// Actions
export const { setLoading, setNftForSell } = nftForSellsSlice.actions

export default nftForSellsSlice.reducer

export const fetchNftForSellAsync = (account, nft, tokenid) => async (dispatch) => {
  const loading = {
    nftKey: nft,

    nftId: tokenid,
  }

  dispatch(setLoading(loading))

  const _isApproced = await fetchAllowance(account, nft, tokenid)

  const _feePercent = await fetchFeePercent()

  const info = 
  {
    nftKey: nft,

    nftId: tokenid,

    isApproved: _isApproced.isApproved,

    owner: _isApproced.owner,

    feePercent: _feePercent
  }

  dispatch(setNftForSell(info))
}