import { AbiItem } from 'web3-utils'
import { Interface } from '@ethersproject/abi'
import getWeb3NoAccount from 'utils/web3'
import { getENVChainId, ChainId } from 'sdk'
import { MULTICALL_ABI, getMulticallAddress } from 'sdk/multicall'

interface Call {
  address: string // Address of the contract
  name: string // Function name on the contract (example: balanceOf)
  params?: any[] // Function params
}

const multicall = async (abi: any[], calls: Call[], input? : ChainId) => {
  const chaindId = (input || getENVChainId())

  const web3 = getWeb3NoAccount(chaindId)

  const multi = new web3.eth.Contract((MULTICALL_ABI as unknown) as AbiItem, getMulticallAddress(chaindId))

  const itf = new Interface(abi)

  const calldata = calls.map((call) => [call.address.toLowerCase(), itf.encodeFunctionData(call.name, call.params)])
 
  const { returnData } = await multi.methods.aggregate(calldata).call()
 
  const res = returnData.map((call, i) => itf.decodeFunctionResult(calls[i].name, call))

  return res
}

export default multicall
