import getOGConfig from 'sdk/nft/og'
import ogABI from 'sdk/nft/og/ogABI.json'
import erc20ABI from 'sdk/abis/erc20.json'
import multicall from 'utils/multicall'
import BigNumber from 'bignumber.js'
 
export const fetchOGAllowanceAndBalance = async (account) => {
  const ogConfig = getOGConfig()

  const calls = [{
     address: ogConfig.payToken.address,

     name: 'allowance',
    
     params: [account, ogConfig.address],
  },

  {
    address: ogConfig.payToken.address,

    name: 'balanceOf',
    
    params: [account],
  }]

  const [allowance, balance] = await multicall(erc20ABI, calls)

  return {
    userallowance: new BigNumber(allowance).toJSON(),

    userbalance: new BigNumber(balance).toJSON()
  }
}

export const fetchOGUserNfts = async (account) => {
  const ogConfig = getOGConfig()

  const calls = [{
    address: ogConfig.address,

    name: 'balanceOf',
   
    params: [account],
 },

 {
   address: ogConfig.address,

   name: 'tokensOf',
   
   params: [account],
 }]

 const [count, tokens] = await multicall(ogABI, calls)

 const { ids, urls } = tokens

 return {
  userNftCounts: count,

  usernfts: ids.map((id, index) =>
  {
    return {
      nftId: id.toString(),

      nftUri: urls[index]
    }
  })
 }
}
 

 