import { TokenType } from './constants'

export interface Token {
  symbol: string
  address?: string
  decimals?: number
  projectLink?: string
  fee?: number
  type?: TokenType
}

export interface Liquidity
{
  symbol: string
  address: string
  token: Token,
  quoteToken: Token,
}

export enum PoolCategory {
  'COMMUNITY' = 'Community',
  'CORE' = 'Core',
  'ETH' = 'ETH'
}

export type Images = {
  lg: string
  md: string
  sm: string
  ipfs?: string
}

export type PageMeta = {
  title: string
  description?: string
  image?: string
}

export interface SyrupPoolConfig {
  syrupId: number
  earningToken: Token
  stakingToken: Token
  poolCategory: PoolCategory
  stakingLimit?: number
  contractAddress: string
  tokenPerBlock: string
  sortOrder?: number
  harvest?: boolean
  isFinished?: boolean
  enableEmergencyWithdraw?: boolean
}

export interface LiquidityPoolConfig {
  liquidityid: number
  liquidity: Liquidity
  earningToken: Token
  contractAddress: string
  isCommunity?: boolean
  rewardPerBlock?: number
}

export interface AirdropConfig
{
   id: number,
   version: number,
   address: string,
   rewardToken: Token
}


export interface BenchmarkPriceConfig
{
  eth : Token,

  usd : Token,

  ethlpaddress : string,
  
  graus : Token,

  quote : Token,

  grauslpaddress: string
}
