import getSyruppools from 'sdk/syruppools'
import syruppoolABI from 'sdk/abis/syruppool.json'
import multicall from 'utils/multicall'
import BigNumber from 'bignumber.js'

export const fetchPoolsBlockLimits = async () => {
  const poolsWithEnd = getSyruppools().filter((p) => p.syrupId !== 0)

  const callsStartBlock = poolsWithEnd.map((p) => {
    return {
      address: p.contractAddress,

      name: 'startBlock',
    }
  })

  const callsEndBlock = poolsWithEnd.map((p) => {
    return {
      address: p.contractAddress,

      name: 'bonusEndBlock',
    }
  })

  const starts = await multicall(syruppoolABI, callsStartBlock)

  const ends = await multicall(syruppoolABI, callsEndBlock)

  return poolsWithEnd.map((p, index) => {
    const startBlock = starts[index]

    const endBlock = ends[index]

    return {
      syrupId: p.syrupId,

      startBlock: new BigNumber(startBlock).toJSON(),

      endBlock: new BigNumber(endBlock).toJSON(),
    }
  })
}

export const fetchPoolsTotalStaking = async () => {
  const pools = getSyruppools()

  const callsPools = pools.map((p) => {
    return {
      address: p.contractAddress,

      name: 'totalStaked'
    }
  })

  const nonBnbPoolsTotalStaked = await multicall(syruppoolABI, callsPools)

  return [
    ...pools.map((p, index) => ({
      syrupId: p.syrupId,

      totalStaked: new BigNumber(nonBnbPoolsTotalStaked[index]).toJSON(),
    }))
  ]
}
