import BigNumber from 'bignumber.js'
import { ADDRESS0, MULTICALL_ABI, getMulticallAddress, MaxUint256 } from 'sdk'
import { getNftMarket } from 'sdk/nft/shop'
import marketABI from 'sdk/nft/shop/marketABI.json'
import shopABI from 'sdk/nft/shop/shopABI.json'
import erc20ABI from 'sdk/abis/erc20.json'
import multicall from 'utils/multicall'

export const fetchAllowance = async (account, paytoken, nft) => {
  if( paytoken === ADDRESS0)
  {
    const ethcalls = [ 
    {
      address: getMulticallAddress(),
  
      name: 'getEthBalance',
    
      params: [account],
    }]
 
   const [ethbalance] = await multicall(MULTICALL_ABI, ethcalls)

     return {
      allowance: MaxUint256,

      balance: new BigNumber(ethbalance).toJSON()
     }
  }

  const market = getNftMarket()

  const shop = market.shops.find((item) => (item.nft === nft))

  const calls = [{
     address: paytoken,

     name: 'allowance',
    
     params: [account, shop.address],
  },

  {
    address: paytoken,

    name: 'balanceOf',
   
    params: [account],
  }]

  const [allowance, balance] = await multicall(erc20ABI, calls)

  return {
    allowance: new BigNumber(allowance).toJSON(),

    balance: new BigNumber(balance).toJSON()
  }
}

export const fetchCurrentPrice = async (nft, tokenid) => {
  const market = getNftMarket()

  const shop = market.shops.find((item) => (item.nft === nft))

  const calls = [{
     address: shop.address,

     name: 'getPrice',
    
     params: [tokenid],
  }]

  const [price] = await multicall(shopABI, calls)

  return new BigNumber(price).toJSON()
}

export const fetchFeePercent = async() => {
    const market = getNftMarket()

    const calls = [{
      address: market.address,

      name: 'getFeePercent',
    }]
  
   const feePercent = await multicall(marketABI, calls) 
  
   return feePercent.toString()
}
 

 