import htTokens from './128'
import bscTokens from './56'
import hscTokens from './70'
import { ChainId, getENVChainId } from '../constants'

const tokens = {
  [ChainId.BSCNET]: bscTokens,

  [ChainId.HTNET]: htTokens,

  [ChainId.HSCNET]: hscTokens
}

export default function getTokens(chainId? : ChainId)
{
   return chainId ? tokens[chainId] : tokens[getENVChainId()]
}