import Web3 from 'web3'
import { HttpProviderOptions } from 'web3-core-helpers'
import { ChainId, getENVChainId, getNodeUrl } from 'sdk'

const httpProviders = 
{
  [ChainId.BSCNET]: new Web3.providers.HttpProvider(getNodeUrl(ChainId.BSCNET), { timeout: 10000 } as HttpProviderOptions),

  [ChainId.HTNET]: new Web3.providers.HttpProvider(getNodeUrl(ChainId.HTNET), { timeout: 10000 } as HttpProviderOptions),

  [ChainId.HSCNET]: new Web3.providers.HttpProvider(getNodeUrl(ChainId.HSCNET), { timeout: 10000 } as HttpProviderOptions),
}

const web3NoAccount =
{
  [ChainId.BSCNET]: new Web3(httpProviders[ChainId.BSCNET]),

  [ChainId.HTNET]: new Web3(httpProviders[ChainId.HTNET]),

  [ChainId.HSCNET]: new Web3(httpProviders[ChainId.HSCNET]),
}

const getWeb3NoAccount = (chainId?: ChainId) => 
{
  return chainId ? web3NoAccount[chainId] : web3NoAccount[getENVChainId()]
}

export default getWeb3NoAccount 
