import React from 'react'
import { Flex, Button, useModal, Skeleton, Text } from 'uikit'
import { AirdropPool } from 'state/types'
import { useTranslation } from 'contexts/Localization'
import CollectModal from '../Modals/CollectModal'

interface HarvestActionsProps {
  pool: AirdropPool
  invitor?: string,
  isLoading?: boolean
}

const HarvestActions: React.FC<HarvestActionsProps> = ({
  pool,
  invitor,
  isLoading = false
}) => {
  const { t } = useTranslation()

  const baseAirdropAmount = pool.info?.baseAirdropAmount

  const sgeOffset = pool.info?.sgeOffset

  const minDropAmount = baseAirdropAmount - baseAirdropAmount * sgeOffset / 100

  const maxDropAmount = baseAirdropAmount + baseAirdropAmount * sgeOffset / 100

  const [onPresentCollect] = useModal(
    <CollectModal
      invitor={invitor}
      id={pool.id}
      minDropAmount={minDropAmount}
      maxDropAmount={maxDropAmount}
      rewardTokenSymbol={pool.rewardToken.symbol}
    />,
  )

  return (
    <Flex flexDirection="column" mb="16px">
      <Flex justifyContent="space-between" alignItems="center">
        <Flex flexDirection="column">
          {isLoading ? (
            <Skeleton width="80px" height="48px" />
          ) : (
            <>
            <Flex justifyContent="space-between" mb='5px'>
             <Text ml='5px'>{t('AirdropEarnMin')}:</Text>
             <Text mr='5px'>{minDropAmount}</Text>
            </Flex>
            <Flex justifyContent="space-between" mb='5px'>
             <Text ml='5px'>{t('AirdropEarnMax')}:</Text>
             <Text mr='5px'>{maxDropAmount}</Text>
            </Flex>
            </>
          )}
        </Flex>
        <Flex>
          <Button onClick={onPresentCollect}>
            { t('Harvest')}
          </Button>
        </Flex>
      </Flex>
    </Flex>
  )
}

export default HarvestActions
