import htconfig from './128'
import bscconfig from './56'
import hscconfig from './70'
import { ogconfig } from '../types'
import { ChainId, getENVChainId } from '../../constants'

const ogconfigs = {
  [ChainId.BSCNET]: bscconfig,

  [ChainId.HTNET]: htconfig,

  [ChainId.HSCNET]: hscconfig
}

export default function getOGConfig(chainId? : ChainId) : ogconfig
{
   return chainId ? ogconfigs[chainId] : ogconfigs[getENVChainId()]
}