import useWeb3 from 'hooks/useWeb3'
import { useMemo } from 'react'
import { Contract } from '@ethersproject/contracts'
import getOGConfig from 'sdk/nft/og'
import ogABI from 'sdk/nft/og/ogABI.json'
import shopABI from 'sdk/nft/shop/shopABI.json'
import getSyruppools from 'sdk/syruppools'
import syruppoolABI from 'sdk/abis/syruppool.json'
import getLiquiditypools from 'sdk/liquiditypools'
import liquiditypoolABI from 'sdk/abis/liquiditypool.json'
import getAirdrops from 'sdk/airdrops'
import airdropABI from 'sdk/abis/airdrop.json'
import{ getContract } from 'utils/index'
import getWETHABI from 'sdk/abis/WETH'
import getPairABI from 'sdk/abis/Pair'
import {
  getBep20Contract,
  getErc721Contract,
  getGrausContract,
  getSyrupPoolContract,
  getLiquidityPoolContract
} from 'utils/contractHelpers'
import { ChainId, MULTICALL_ABI, getMulticallAddress, ERC20, ERC20_BYTES32_ABI, ENS_REGISTER, ENS_PUBLIC_RESOLVER, getWETH, ERC721 } from 'sdk'
import { useActiveWeb3React } from './index'
 
/**
 * Helper hooks to get specific contracts (by ABI)
 */
 function useContract(address: string | undefined, ABI: any, withSignerIfPossible = true): Contract | null {
  const { library, account } = useActiveWeb3React()

  return useMemo(() => {
    if (!address || !ABI || !library) return null
    try {
      return getContract(address, ABI, library, withSignerIfPossible && account ? account : undefined)
    } catch (error) {
      console.error('Failed to get contract', error)
      return null
    }
  }, [address, ABI, library, withSignerIfPossible, account])
}

export function useWETHContract(chainId: ChainId, withSignerIfPossible?: boolean): Contract | null {
  return useContract(chainId ? getWETH().address : undefined, getWETHABI(chainId), withSignerIfPossible)
}

export function useTokenContract(tokenAddress?: string, withSignerIfPossible?: boolean): Contract | null {
  return useContract(tokenAddress, ERC20, withSignerIfPossible)
}

export function useNftContract(tokenAddress?: string, withSignerIfPossible?: boolean): Contract | null {
  return useContract(tokenAddress, ERC721, withSignerIfPossible)
}

export const useOGContract = () => {
  const config = getOGConfig()

  return useContract(config.address, ogABI)
}

export const useNftShopContract = (address: string) => {
  return useContract(address, shopABI)
}

export const useSyrupPoolContract = (id) => {
  const config = getSyruppools().find((pool) => pool.syrupId === id)

  return useContract(config.contractAddress, syruppoolABI)
}

export const useLiquidityPoolContract = (id) => {
  const config = getLiquiditypools().find((pool) => pool.liquidityid === id)

  return useContract(config.contractAddress, liquiditypoolABI)
}

export const useAirdropContract = (id) =>
{
  const config = getAirdrops().find((p)=> p.id === id)

  return useContract(config.address, airdropABI)
}

export function usePairContract(chainId: ChainId, pairAddress?: string, withSignerIfPossible?: boolean): Contract | null {
  return useContract(pairAddress, getPairABI(chainId), withSignerIfPossible)
}

export function useBytes32TokenContract(tokenAddress?: string, withSignerIfPossible?: boolean): Contract | null {
  return useContract(tokenAddress, ERC20_BYTES32_ABI, withSignerIfPossible)
}

export function useMulticallContract(chainId: ChainId): Contract | null {
  return useContract(getMulticallAddress(chainId), MULTICALL_ABI, false)
}

export const useERC20 = (address: string, chainId: ChainId) => {
  const web3 = useWeb3(chainId)

  return useMemo(() => getBep20Contract(address, chainId, web3), [address, web3, chainId])
}

/**
 * @see https://docs.openzeppelin.com/contracts/3.x/api/token/erc721
 */
export const useERC721 = (address: string, chainId: ChainId) => {
  const web3 = useWeb3(chainId)

  return useMemo(() => getErc721Contract(address, chainId, web3), [address, web3, chainId])
}

export const useSyrupPool = (id, chainId: ChainId) => {
  const web3 = useWeb3(chainId)

  return useMemo(() => getSyrupPoolContract(id, chainId, web3), [id, web3, chainId])
}

export const useLiquidityPool = (id, chainId: ChainId) => {
  const web3 = useWeb3(chainId)

  return useMemo(() => getLiquidityPoolContract(id, chainId, web3), [id, web3, chainId])
}

export const useGraus = (chainId: ChainId) => {
  const web3 = useWeb3(chainId)

  return useMemo(() => getGrausContract(chainId, web3), [web3, chainId])
}

export function useENSRegistrarContract(withSignerIfPossible?: boolean): Contract | null {
  let address: string | undefined
 
  return useContract(address, ENS_REGISTER, withSignerIfPossible)
}

export function useENSResolverContract(address: string | undefined, withSignerIfPossible?: boolean): Contract | null {
  return useContract(address, ENS_PUBLIC_RESOLVER, withSignerIfPossible)
}
 