import { useCallback } from 'react'
import { useAppDispatch } from 'state'
import { setOnSellPageQCVersion } from 'state/actions'
import { useNftShopContract } from './useContract'
import { calculateGasMargin } from '../utils'   

export default function useNftSellUpdatePrice(
  shop: string, tokenid: string, price: string, callback: any
): [ () => Promise<void>] {
  const shopContract = useNftShopContract(shop)

  const dispatch = useAppDispatch()
  
  const onUpdatePrice = useCallback(async (): Promise<void> => {
    if (!shopContract) {
      console.error('nft shop contract is null')

      return
    }

    const estimatedGas = await shopContract.estimateGas.updatePrice(tokenid, price)
                                                      
    // eslint-disable-next-line consistent-return
    shopContract
      .updatePrice(tokenid, price, { gasLimit: calculateGasMargin(estimatedGas) })
      .then(() => {
                const interval = setInterval(async () => {
                     dispatch(setOnSellPageQCVersion())
                    
                     clearInterval(interval)   
  
                     callback()     
                   
                  
                }, 6000)   
        })
        .catch((error: Error) => {
          console.error('Failed to update price', error)
          throw error
        })
  }, [shopContract, callback, tokenid, price, dispatch])

  return [ onUpdatePrice ]
}

 
