import invariant from 'tiny-invariant'
import { ChainId } from '../constants'
import Currency from './currency'
import { getWETH, getETH } from '../values'
import Token from './token'
import Price from './fractions/price'
import Pair from './pair'

class Route {
  public readonly pairs: Pair[]

  public readonly path: Token[]

  public readonly input: Currency

  public readonly output: Currency

  public readonly midPrice: Price

  public constructor(pairs: Pair[], input: Currency, output?: Currency) {
    invariant(pairs.length > 0, 'PAIRS')
    invariant(
      pairs.every(pair => pair.chainId === pairs[0].chainId),
      'CHAIN_IDS'
    )
    invariant(
      (input instanceof Token && pairs[0].involvesToken(input)) ||
        (input === getETH() && pairs[0].involvesToken(getWETH())),
      'INPUT'
    )
    invariant(
      typeof output === 'undefined' ||
        (output instanceof Token && pairs[pairs.length - 1].involvesToken(output)) ||
        (output === getETH() && pairs[pairs.length - 1].involvesToken(getWETH())),
      'OUTPUT'
    )

    const path: Token[] = [input instanceof Token ? input :getWETH()]

    for (let i = 0; i < pairs.length; i++) {
      const pair = pairs[i];
      const currentInput = path[i]
      invariant(currentInput.equals(pair.token0) || currentInput.equals(pair.token1), 'PATH')
      const output_value = currentInput.equals(pair.token0) ? pair.token1 : pair.token0
      path.push(output_value)
    }

    this.pairs = pairs
    this.path = path
    this.midPrice = Route.fromRoute(this)
    this.input = input
    this.output = output ?? path[path.length - 1]
  }

  public static fromRoute(route: Route): Price {
    const prices: Price[] = []
    for (let i = 0; i < route.pairs.length; i++) {
      const pair = route.pairs[i]; 
      prices.push(
        route.path[i].equals(pair.token0)
          ? new Price(pair.reserve0.currency, pair.reserve1.currency, pair.reserve0.raw, pair.reserve1.raw)
          : new Price(pair.reserve1.currency, pair.reserve0.currency, pair.reserve1.raw, pair.reserve0.raw)
      )
    }
    return prices.slice(1).reduce((accumulator, currentValue) => accumulator.multiply(currentValue), prices[0])
  }

  public get chainId(): ChainId {
    return this.pairs[0].chainId
  }
}

export default Route
