import React, { useState } from 'react'
import { parseUnits } from '@ethersproject/units'
import styled from 'styled-components'
import BigNumber from 'bignumber.js'
import { getNftPayTokens } from 'sdk/nft/shop'
import { getETHId, ADDRESS0 } from 'sdk'
import { Button, Modal, Flex, Text } from 'uikit'
import { onPriceAmountKeyDown } from 'utils/checkInput'
import { getBalanceNumber } from 'utils/formatBalance'
import { useGetApiPrices } from 'state/hooks'
import { useTranslation } from 'contexts/Localization'
import useNftSellUpdatePrice from 'hooks/useNftSellUpdatePrice'
import { InputLabel } from 'components/Label'
 
interface NftOnSellUpdatePriceModalProps {
  shop: string,

  tokenid: string,

  paytoken: string,

  price: BigNumber,

  onDismiss?: () => void,

  onSuccess?: () => void
}

const StyledInput = styled.input`
  width: 200px;  
  position: relative;
  display: flex;
  padding: 5px;
  margin-right: 5px;
  align-items: center;
  white-space: nowrap;
  text-align: right;
  background: none;
  border: none;
  outline: none;
  border-radius: 12px;
  color: ${({ theme }) => theme.colors.text};
  border-style: solid;
  border: 1px solid ${({ theme }) => theme.colors.primary};
  -webkit-appearance: none;

  font-size: 12px;

  ::placeholder {
    color: ${({ theme }) => theme.colors.textDisabled};
  }
  transition: border 100ms;
  :focus {
    outline: none;
  }
`

const CaptionContainer = styled.div`
  min-height: 24px;
  display: flex;
  align-items: center;
`

const ContentContainer = styled.div`
  min-height: 24px;
  display: flex;
  align-items: center;
`

const NftOnSellUpdatePriceModal: React.FC<NftOnSellUpdatePriceModalProps> = ({ shop, tokenid, price, paytoken, onDismiss, onSuccess }) => {
  const [pendingTx, setPendingTx] = useState(false)

  const [commitResult, setCommitResult] = useState({ step: 'step 1', message: 'input new price', success: true })

  const { t } = useTranslation()

  const prices = useGetApiPrices()

  const { ethPrice } = prices

  const paytokens = getNftPayTokens()

  const priceTokenPrice = paytoken === ADDRESS0 ? ethPrice : prices[paytoken]

  const priceTokenSymbol = paytoken === ADDRESS0 ? getETHId() : paytokens.find((token) => token.address === paytoken).symbol

  const payTokenDecimals =paytoken === ADDRESS0 ? 18 : paytokens.find((token) => token.address === paytoken).decimals

  const oldpriceNumber = getBalanceNumber(price, payTokenDecimals)  

  const oldpriceDollar = oldpriceNumber * priceTokenPrice

  const isoldHighValue = Math.round(oldpriceDollar * 1000) < 1

  const [newPriceText, setNewPriceText] = useState(0.0)

    function getNewPriceDollar(p)
    {
      const priceDollar = p * priceTokenPrice

      const isHighValue = Math.round(priceDollar * 1000) < 1

      return priceDollar.toFixed(isHighValue ? 4 : 2)
    }

    const [newPriceDollarText, setNewPriceDollarText] = useState(getNewPriceDollar(newPriceText))

    const onPriceChange = (e: React.ChangeEvent<HTMLInputElement>) => {
      const _price = Number.parseFloat(e.target.value)
  
      setNewPriceDollarText(getNewPriceDollar(Number.isNaN(_price) ? 0 : _price))
     
      setNewPriceText(Number.isNaN(_price) ? 0 : _price)
    }

  const updatePriceCallback = () =>
  {
    setPendingTx(false)

    setCommitResult({
      success: true,

      step: "end",

      message: "success"
    })

    if(onSuccess)
    {
      onSuccess()
    }

    onDismiss()
  }

  const [ onUpdatePrice ] = useNftSellUpdatePrice(shop, tokenid, parseUnits(newPriceText > 0 ? newPriceText.toString() : '0', payTokenDecimals).toString(), updatePriceCallback)

  const handleOnUpdatePrice = async () => {
      try {
        if(newPriceText <= 0 || Number.isNaN(newPriceText))
        {
          setCommitResult({
            success: false,
      
            step: "step 2",
      
            message: t('price must large than 0')
          }) 
        }

        else
        {
          commitResult.success = true

          commitResult.message = t('waiting for commit')

         setPendingTx(true)

         await onUpdatePrice()
        }
      } catch (e) {

        setPendingTx(false)

        setCommitResult({
          success: false,
    
          step: "step 2",
    
          message: e.message
        }) 
      }
  }

  const onPriceKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) =>
  {
    onPriceAmountKeyDown(e, payTokenDecimals)
  }

  return (
    <Modal title={t('Update Ask')} onDismiss={onDismiss} >
      <Flex maxWidth='420px' minWidth='420px' alignItems="center" flexDirection="column" justifyContent="space-between" mb="8px">
        <Text bold color="textSubtle" fontSize="18px" pr="3px">
         {t(commitResult.step)}
        </Text>
        { commitResult.success ? (<Text color="textSubtle" fontSize="14px" pr="3px" mt='10px'>
         {t(commitResult.message)}
        </Text>) :
        (<Text bold color="failure" fontSize="14px" pr="3px" mt='10px'>
         {t(commitResult.message)} 
        </Text> )}
      </Flex>

      <Flex flexDirection="column" width='100%'>
       <Flex flexDirection="row" mt='10px' mb='5px' justifyContent="space-between">
        <CaptionContainer>
         <InputLabel text={t('Old Price')}/>
        </CaptionContainer>
        <ContentContainer>
           <Flex flexDirection="row" alignItems="center">
           {`${oldpriceNumber} ${priceTokenSymbol} ≈ ${oldpriceDollar.toFixed(isoldHighValue ? 4 : 2)} $`}
           </Flex>
        </ContentContainer>
       </Flex>

       <Flex flexDirection="row" mt='5px' mb='5px' justifyContent="space-between">
         <CaptionContainer>
           <InputLabel text={t('New Price')} isNecessary/>
         </CaptionContainer>
        <ContentContainer>
        <Flex flexDirection="row" alignItems="center">
           <StyledInput onKeyDown={onPriceKeyDown} defaultValue='0' placeholder={t('')} className="token-amount-input" onChange={onPriceChange} type='text'/>
           {`${priceTokenSymbol} ≈ ${newPriceDollarText} $`}
           </Flex>
        </ContentContainer>
       </Flex>
       </Flex>

      <Button
        isLoading={pendingTx}
        onClick={handleOnUpdatePrice}
        mt="24px"
      >
        {pendingTx ? t('Committing') : t('Commit')}
      </Button>
    </Modal>
  )
}

export default NftOnSellUpdatePriceModal
