import { createSlice } from '@reduxjs/toolkit'
import BigNumber from 'bignumber.js'
import { nftOnSellNode, nftOnSellPageModule, nftOnSellPageState } from '../types'
import { fetchAsks, fetchPaytokenAsks, fetchAsksByTokenIds } from './fetchNftOnSells'

const initialState: nftOnSellPageState = 
{
     data: new Map<number, nftOnSellPageModule>() 
}

export const nftOnSellsSlice = createSlice({
    name: 'nftOnSells',
  
    initialState,

    reducers: {
        setLoading:  (state, action) => {
          const info = action.payload

          state.data.clear()
     
          const newNode = 
          {
            loading: true,
  
            module: undefined,

            hashasks: new Map<string, nftOnSellNode>()
          }

          state.data.set(info.pageVersion, newNode)
      },

      setHashLoading:  (state, action) => {
        const info = action.payload

        const nodeIn = state.data.get(info.pageVersion)

        if(!nodeIn)
        {
           const newModule = 
           {
             loading: false,

             module: undefined,
         
             hashasks: new Map<string, nftOnSellNode>()
           }

           state.data.set(info.pageVersion, newModule)
        }

        const node = state.data.get(info.pageVersion)

        node.hashasks.clear()
   
        const newNode = 
        {
          loading: true,

          owner: undefined,
  
          paytoken: undefined,
        
          tokenid: info.tokenid,
          
          price: undefined,
        }

        node.hashasks.set(info.nftKey, newNode)
    },

    setHashAsk:  (state, action) => {
      const info = action.payload

      const node = state.data.get(info.pageVersion)

      const asknode = node.hashasks.get(info.nftKey)
 
      if(asknode)
      {
        asknode.loading = false

        asknode.owner = info.owner

        asknode.paytoken = info.paytoken
      
        asknode.tokenid = info.tokenid
        
        asknode.price = info.price
      }
      },

      setPageModule:  (state, action) => {
        const info = action.payload

        const node = state.data.get(info.pageVersion)

        node.loading = false

        node.module = info.pageMoudle
      }
    }
})

// Actions
export const { setLoading, setPageModule, setHashLoading, setHashAsk } = nftOnSellsSlice.actions

export default nftOnSellsSlice.reducer

export const fetchAsksAsync = (nft, version, page, size, desc?) => async (dispatch) => {
    const loading = {
      pageVersion: version
    }
  
    dispatch(setLoading(loading))
  
    const _ask_result = await fetchAsks(nft, page, size, desc)

    const info = updatePageModule(nft, version, _ask_result)

    dispatch(setPageModule(info))
  }

  function updatePageModule(nftKey, version, _ask_result)
  {
    const _ask_nodes = _ask_result.asks.map((item) =>
    {
      const [_owner, _paytoken, _tokenid, _price] = item

      return {
        owner: _owner,

        paytoken: _paytoken,

        tokenid: _tokenid.toString(),

        price: new BigNumber(_price.toString())
      } as nftOnSellNode
    })

    const _module = 
    {
       nft: nftKey,

       totalCount: _ask_result.count.toString(),

       asks: _ask_nodes
    }

    return {
      pageVersion: version,

      pageMoudle: _module
    }
  }

  export const fetchPayTokenAsksAsync = (nft, version, paytoken, page, size, desc?) => async (dispatch) => {
    const loading = {
      pageVersion: version
    }
  
    dispatch(setLoading(loading))
  
    const _ask_result = await fetchPaytokenAsks(nft, paytoken, page, size, desc)

    const info = updatePageModule(nft, version, _ask_result)

    dispatch(setPageModule(info))
  }

  export const fetchAskByIdAsync = (nft, version, id) => async (dispatch) => {
    const loading = {
      pageVersion: version,

      nftKey: `${nft}@${id}`,

      tokenid: id
    }
  
    dispatch(setHashLoading(loading))
  
    const _ask_result = await fetchAsksByTokenIds(nft, [Number.parseInt(id)])
  
    const [_owner, _paytoken, _tokenid, _price] = _ask_result.asks[0] || []
    
    const info = 
    {
      pageVersion: version,

      nftKey: `${nft}@${id}`,

      tokenid: _tokenid,

      owner: _owner,

      paytoken: _paytoken,

      price: _price ? new BigNumber(_price.toString()) : undefined
    }
    
    dispatch(setHashAsk(info))
  }

  