/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit'
import { getNftMarket } from 'sdk/nft/shop'
import { fetchAllowance, fetchCurrentPrice, fetchFeePercent } from './fetchNftOnSellBuyUser'
import { nftOnSellBuyNode, nftOnSellBuy, nftOnSellBuyState } from '../types'

const { shops }= getNftMarket()

const initialState: nftOnSellBuyState = { data: shops.map((shop)=>
 { 
   const item = { 
    nft: shop.nft,

    onbuys: new Map<string, nftOnSellBuyNode>()
   } as nftOnSellBuy

   return item
 })}

export const nftOnSellBuysSlice = createSlice({
  name: 'nftOnSellBuys',

  initialState,
  
  reducers: {
    setLoading:  (state, action) => {
      const info = action.payload

      const node = state.data.find((item) => item.nft === info.nftKey)

      if(node)
      {
        const sellNodeIn = node.onbuys.get(info.nftId)

        if(sellNodeIn)
        {
          sellNodeIn.loading = true
        }

        else 
        {
          const sellNode = 
          {
            loading: true,
  
            allowance: undefined,
  
            feePercent: 0,

            lastprice: undefined,

            balance: undefined
          }

          node.onbuys.set(info.nftId, sellNode)
        }
      }
    },

    setNftOnSellBuy: (state, action) => {
      const info = action.payload

      const node = state.data.find((item) => item.nft === info.nftKey)

      if(node)
      {
        const sellNodeIn = node.onbuys.get(info.nftId)

        if(sellNodeIn)
        {
          sellNodeIn.loading = false

          sellNodeIn.allowance = info.allowance

          sellNodeIn.lastprice = info.price

          sellNodeIn.feePercent = info.feePercent

          sellNodeIn.balance = info.balance
        }

        else 
        {
          const sellNode = 
          {
            loading: false,
  
            allowance: info.allowance,
  
            feePercent: info.feePercent,

            lastprice: info.price,

            balance: info.balance
          }
  
          node.onbuys.set(info.nftId, sellNode)
        }
      }
    },

    updateBalance: (state, action) => {
      const info = action.payload

      const node = state.data.find((item) => item.nft === info.nftKey)

      if(node)
      {
        const sellNodeIn = node.onbuys.get(info.nftId)

        if(sellNodeIn)
        {
          sellNodeIn.loading = false

          sellNodeIn.allowance = info.allowance

          sellNodeIn.balance = info.balance
        }
      }
    }
  }
})

// Actions
export const { setLoading, setNftOnSellBuy, updateBalance } = nftOnSellBuysSlice.actions

export default nftOnSellBuysSlice.reducer

export const fetchNftOnSellBuyAsync = (account, nft, paytoken, tokenid) => async (dispatch) => {
  const loading = {
    nftKey: nft,

    nftId: tokenid,
  }

  dispatch(setLoading(loading))

  const _allowance = await fetchAllowance(account, paytoken, nft)

  const _feePercent = await fetchFeePercent()

  const _price = await fetchCurrentPrice(nft, tokenid) 

  const info = 
  {
    nftKey: nft,

    nftId: tokenid,

    allowance: _allowance.allowance,

    balance: _allowance.balance,

    feePercent: _feePercent,

    price: _price
  }

  dispatch(setNftOnSellBuy(info))
}

export const fetchPayAllowanceAsync = (account, nft, paytoken, tokenid) => async (dispatch) => {
  const _allowance = await fetchAllowance(account, paytoken, nft)

  const info = 
  {
    nftKey: nft,

    nftId: tokenid,

    allowance: _allowance.allowance,

    balance: _allowance.balance,
  }

  dispatch(updateBalance(info))
}