import getOGConfig from 'sdk/nft/og'
import ogABI from 'sdk/nft/og/ogABI.json'
import multicall from 'utils/multicall'
import BigNumber from 'bignumber.js'

const fetchOGCreateFee = async () => {
  const ogConfig = getOGConfig()

  const calls= [{
    address: ogConfig.address,

    name: 'createFee',
  }]

  const [createFee] = await multicall(ogABI, calls)

  return new BigNumber(createFee).toJSON()
}
 
export default fetchOGCreateFee
