import React from 'react'
import styled from 'styled-components'
import { Flex, Text, Box } from 'uikit'
import { useTranslation } from 'contexts/Localization'
import { AirdropPool } from 'state/types'
import HarvestActions from './HarvestActions'

const InlineText = styled(Text)`
  display: inline;
`

interface CardActionsProps {
  pool: AirdropPool
}

const CardActions: React.FC<CardActionsProps> = ({ pool }) => {
  const { t } = useTranslation()

  const remainingCount = (pool.info.airdropCount - pool.info.hasAirdropCount)

  const harvest = (remainingCount > 0) && pool.userData && (pool.userData.userAirdropAmount === 0)

  const isLoading = !pool.userData
  
  function getQueryVariable(name: string) : string 
  {
       let query = window.location.search.substring(1)

       if(query.indexOf('?') === -1)
       {
          query = window.location.hash.split("?")[1]
       }

       if(!query)
       {
          return undefined
       }

       const vars = query.split("&")

       for (let i = 0; i < vars.length; i++) {
          const pair = vars[i].split("=")
        
          if(pair[0] === name)
          {
            return pair[1]
          }
       }

       return undefined
  }

  return (
    <Flex flexDirection="column">
      <Flex flexDirection="column">
        {harvest && (
          <>
            <Box display="inline">
              <InlineText color="secondary" textTransform="uppercase" bold fontSize="12px">
                {`${pool.rewardToken.symbol} `}
              </InlineText>
              <InlineText color="textSubtle" textTransform="uppercase" bold fontSize="12px">
                {t(`Earned`)}
              </InlineText>
            </Box>
            <HarvestActions
              invitor={getQueryVariable("invitor")}
              pool={pool}
              isLoading={isLoading} 
            />
          </>
        )}
      </Flex>
    </Flex>
  )
}

export default CardActions
