import { useCallback, useMemo } from 'react'
import { useActiveWeb3React } from 'hooks'
import { useAppDispatch } from 'state'
import { fetchNftForSellAsync } from 'state/actions'
import { fetchAllowance } from 'state/nftForSell/fetchNftForSellUser'
import { calculateGasMargin } from '../utils'
import { ApprovalState } from './useApproveCallback'
import { useNftContract } from './useContract'

export default function useNftSellAskApprove(nft: string, shop: string, tokenid: string, callback: any): [ApprovalState, () => Promise<void>] {
  const { account } = useActiveWeb3React()

  const dispatch = useAppDispatch()

  const nftContract = useNftContract(nft)

  let isApproved

  fetchAllowance(account, nft, tokenid)
 .then((result)=>
  {
    isApproved = result
  })

  // check the current approval status
  const approvalState: ApprovalState = useMemo(() => {
    if (!nftContract) return ApprovalState.UNKNOWN

    // amountToApprove will be defined if currentAllowance is
    return isApproved ? ApprovalState.APPROVED : ApprovalState.NOT_APPROVED
  }, [nftContract, isApproved])

  const approve = useCallback(async (): Promise<void> => {
    if (approvalState !== ApprovalState.NOT_APPROVED) {
      console.error('approve was called unnecessarily')

      return
    }

    if (!nft) {
      console.error('nft contract is null')

      return
    }

    if (!shop) {
      console.error('no shop')

      return
    }

    const estimatedGas = await nftContract.estimateGas.setApprovalForAll(shop, true)

    // eslint-disable-next-line consistent-return
    nftContract
           .setApprovalForAll(shop, true, { gasLimit: calculateGasMargin(estimatedGas) })
           .then(() => {
              const interval = setInterval(async () => {
                dispatch(fetchNftForSellAsync(account, nft, tokenid)).then(
                  () =>
                  {
                   clearInterval(interval)   

                   callback()     
                  }
                 )
              }, 6000)
      })
      .catch((error: Error) => {
        console.error('Failed to approve token', error)
        throw error
      })
  }, [approvalState, nftContract, nft, shop, tokenid, dispatch, account, callback])

  return [approvalState, approve]
}
