import React, { useState, useCallback, useEffect } from 'react'
import styled from 'styled-components'
import BigNumber from 'bignumber.js'
import { Button, useModal } from 'uikit'
import { useTranslation } from 'contexts/Localization'
import useToast from 'hooks/useToast'
import useNftOnSellBuyApprove from 'hooks/useNftOnSellBuyApprove'
import { ApprovalState } from 'hooks/useApproveCallback'
import { useAppDispatch } from 'state'
import { fetchNftOnSellBuyAsync } from 'state/actions'
import { useNftOnSellBuy } from 'state/hooks'
import { getBalanceNumber } from 'utils/formatBalance'
import NftOnSellBuyModal from './NftOnSellBuyModal'

const Action = styled.div`
  padding-top: 16px;
`
interface NftOnSellBuyActionsProps 
{
  account: string,

  nft: string,

  paytoken: string,

  payTokenDecimals: number,

  price: BigNumber,

  tokenid: string,

  onCommitSuccess: () => void
}

const NftOnSellBuyActions: React.FC<NftOnSellBuyActionsProps> = ({ account, nft, paytoken, payTokenDecimals, price, tokenid, onCommitSuccess }) => {
  const { t } = useTranslation()

  const [requestedApproval, setRequestedApproval] = useState(false)

  const nftOnSellBuy = useNftOnSellBuy(nft, tokenid)

  const dispatch = useAppDispatch()

  useEffect(() => {
   if (!nftOnSellBuy)
   {  
     dispatch(fetchNftOnSellBuyAsync(account, nft, paytoken, tokenid))
   }
 }, [dispatch, nftOnSellBuy, account, nft, tokenid, paytoken])

  const { allowance, lastprice } = nftOnSellBuy || { }

  const allowanceNumber = allowance ? getBalanceNumber(allowance, payTokenDecimals) : -1

  const priceNumber = price ? getBalanceNumber(price, payTokenDecimals) : 0

  const lastpriceNumber = lastprice ? getBalanceNumber(lastprice, payTokenDecimals) : 0

  const isApproved = account && (allowanceNumber >= (lastpriceNumber > 0 ? lastpriceNumber : priceNumber))  
  
  const { toastSuccess, toastError } = useToast()

  const approveCallback = () =>
  {
    toastSuccess(`${t('Contract Enabled')}`, `${t(`You can now buy nft!`)}`)
       
    setRequestedApproval(false)
  }

  const [ approveState, onApprove ] = useNftOnSellBuyApprove(paytoken, nft, tokenid, approveCallback)

  const handleApprove = useCallback(async () => {
    try {
      setRequestedApproval(true)

      await onApprove()

      if (approveState ===  ApprovalState.UNKNOWN) {
        // user rejected tx or didn't go thru
        toastError(
          `${t('Error')}`,
          `${t(`Please try again. Confirm the transaction and make sure you are paying enough gas!`)}`,
        )

        setRequestedApproval(false)
      }  else if (approveState === ApprovalState.APPROVED)
      {
        toastSuccess(`${t('Contract Enabled')}`, `${t(`You can now buy nft!`)}`)
       
        setRequestedApproval(false)
      }

    } catch (e) {
      console.error(e)
    }
  }, [onApprove, approveState, toastSuccess, toastError, t])

  const [onPresentBuyModal] = useModal(<NftOnSellBuyModal account={account} nft={nft} tokenid={tokenid} paytoken={paytoken} price={price} onSuccess={onCommitSuccess}/>)

  const onPresentBuy = () =>
  {
    onPresentBuyModal()
  }

  const renderApprovalOrCreateButton = () => {
    return isApproved ? (
      <Button mt="8px" width="100%" onClick={onPresentBuy}>
        {t('Buy')}
      </Button>
    ) : (
      <Button mt="8px" width="100%" disabled={requestedApproval} onClick={handleApprove}>
        {t('Enable')}
      </Button>
    )
  }

  return (
    <Action>
      {renderApprovalOrCreateButton()}
    </Action>
  )
}

export default NftOnSellBuyActions
