import { getETH, ChainId, getWETH } from 'sdk'
import Token from 'sdk/entities/token'
import Currency from 'sdk/entities/currency'
import TokenAmount from 'sdk/entities/fractions/tokenAmount'
import CurrencyAmount from 'sdk/entities/fractions/currencyAmount'

export function wrappedCurrency(currency: Currency | undefined, chainId: ChainId | undefined): Token | undefined {
  // eslint-disable-next-line no-nested-ternary
  return chainId && currency === getETH(chainId) ? getWETH(chainId) : currency instanceof Token ? currency : undefined
}

export function wrappedCurrencyAmount(
  currencyAmount: CurrencyAmount | undefined,
  chainId: ChainId | undefined
): TokenAmount | undefined {
  const token = currencyAmount && chainId ? wrappedCurrency(currencyAmount.currency, chainId) : undefined
  return token && currencyAmount ? new TokenAmount(token, currencyAmount.raw) : undefined
}

export function unwrappedToken(token: Token): Currency {
  if (token.equals(getWETH(token.chainId))) return getETH(token.chainId)

  return token
}
