import { useEffect, useState } from 'react'
import BigNumber from 'bignumber.js'
import { useActiveWeb3React } from 'hooks'
import { getBep20Contract, getGrausContract } from 'utils/contractHelpers'
import erc20ABI from 'sdk/abis/erc20.json'
import { BIG_ZERO, ChainId, TokenType } from 'sdk'
import getBenchmarkPriceLp from 'sdk/benchmarkprices'
import multicall from 'utils/multicall'
import { getBalanceNumber } from 'utils/formatBalance'
import useRefresh from './useRefresh'

const useTokenBalance = (tokenAddress: string, chainId : ChainId) => {
  const [balance, setBalance] = useState(BIG_ZERO)

  const { account } = useActiveWeb3React()

  const { fastRefresh } = useRefresh()

  useEffect(() => {
    const fetchBalance = async () => {
      const contract = getBep20Contract(tokenAddress, chainId)

      const res = await contract.methods.balanceOf(account).call()
      
      setBalance(new BigNumber(res))
    }

    if (account) {
      fetchBalance()
    }
  }, [account, tokenAddress, fastRefresh, chainId])

  return balance
}

export const useTotalSupply = (chainId : ChainId) => {
  const { slowRefresh } = useRefresh()
  const [totalSupply, setTotalSupply] = useState<BigNumber>()

  useEffect(() => {
    async function fetchTotalSupply() {
      const grausContract = getGrausContract(chainId)
 
      const supply = await grausContract.methods.totalSupply().call()

      setTotalSupply(new BigNumber(supply))
    }

    fetchTotalSupply()
  }, [slowRefresh, chainId])

  return totalSupply
}

export const useBurnedBalance = (tokenAddress: string, chainId: ChainId) => {
  const [balance, setBalance] = useState(BIG_ZERO)
  const { slowRefresh } = useRefresh()
 
  useEffect(() => {
    const fetchBalance = async () => {
      const contract = getBep20Contract(tokenAddress, chainId)
 
      const res = await contract.methods.balanceOf('0x000000000000000000000000000000000000dEaD').call()

      setBalance(new BigNumber(res))
    }

    fetchBalance()
  }, [tokenAddress, slowRefresh, chainId])

  return balance
}

export const useGrausPrice = (chainId: ChainId) => {
  const [grausPrice, setGrausPrice] = useState(0)

  const { slowRefresh } = useRefresh()

   useEffect(() => {
    const config = getBenchmarkPriceLp(chainId)

    const fetchGrausPrice = async () => {
      const calls = [
        // Balance of token in the LP contract
        {
          address: config.usd.address,

          name: 'balanceOf',
          
          params: [config.ethlpaddress],
        },

        // Balance of quote token on LP contract
        {
          address: config.eth.address,

          name: 'balanceOf',
          
          params: [config.ethlpaddress],
        },

        {
          address: config.graus.address,

          name: 'balanceOf',
          
          params: [config.grauslpaddress],
        },

        // Balance of quote token on LP contract
        {
          address: config.quote.address,

          name: 'balanceOf',
          
          params: [config.grauslpaddress],
        },
      ]

      const [
        usd,

        eth,

        graus,

        quote
      ] = await multicall(erc20ABI, calls, chainId)

      const ethPrice =
        getBalanceNumber(new BigNumber(usd), config.usd.decimals) /
        getBalanceNumber(new BigNumber(eth), config.eth.decimals)

      const _grausPrice = config.quote.type === TokenType.ETH ?  
        ( getBalanceNumber(new BigNumber(quote), config.quote.decimals) /
          getBalanceNumber(new BigNumber(graus), config.graus.decimals) * ethPrice) :
        ( getBalanceNumber(new BigNumber(quote), config.quote.decimals) /
          getBalanceNumber(new BigNumber(graus), config.graus.decimals))

      setGrausPrice(_grausPrice)
    }

    fetchGrausPrice()
  }, [slowRefresh, chainId])

  return grausPrice
}

export default useTokenBalance
