import { useCallback } from 'react'
import { UnsupportedChainIdError } from '@web3-react/core'
import { NoBscProviderError } from '@binance-chain/bsc-connector'
import {
  NoEthereumProviderError,
  UserRejectedRequestError as UserRejectedRequestErrorInjected,
} from '@web3-react/injected-connector'
import {
  UserRejectedRequestError as UserRejectedRequestErrorWalletConnect,
  WalletConnectConnector,
} from '@web3-react/walletconnect-connector'
import { ConnectorNames } from 'uikit'
import { getConnectorsByName } from 'connectors'
import useToast from 'hooks/useToast'
import { useActiveWeb3React } from './index'

const useAuth = () => {
  const { activate, deactivate } = useActiveWeb3React()

  const { toastError } = useToast()

  const login = useCallback((connectorID: ConnectorNames) => {

    const connector = getConnectorsByName(connectorID)

    if (connector) {
      activate(connector, async (error: Error) => {
        
        if (!(error instanceof UnsupportedChainIdError)) {
          toastError('Unsupported Chain Id', 'Unsupported Chain Id Error. Check your chain Id.')
        }
         
         else  if (error instanceof NoEthereumProviderError || error instanceof NoBscProviderError) {
            toastError('Provider Error', 'No provider was found')
          } else if (
            error instanceof UserRejectedRequestErrorInjected ||
            error instanceof UserRejectedRequestErrorWalletConnect
          ) {
            if (connector instanceof WalletConnectConnector) {
              const walletConnector = connector as WalletConnectConnector
              walletConnector.walletConnectProvider = null
            }
            toastError('Authorization Error', 'Please authorize to access your account')
          } else {
            toastError(error.name, error.message)
          }
      })
    } else {
      toastError("Can't find connector", 'The connector config is wrong')
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const logout = useCallback(() => {
  //  dispatch(profileClear())
    deactivate()
  }, [deactivate])

  return { login, logout }
}

export default useAuth
