import htLiquidities from './128'
import hscLiquidities from './70'
import bscLiquidities from './56'
import { ChainId, getENVChainId } from '../constants'

const liquidities = {
  [ChainId.BSCNET]: bscLiquidities,

  [ChainId.HTNET]: htLiquidities,

  [ChainId.HSCNET]: hscLiquidities,
}

export default function getLiquidities(chainId? : ChainId)
{
   return chainId ? liquidities[chainId] : liquidities[getENVChainId()]
}