import wht_ABI from './wht.json'
import wbnb_ABI from './wbnb.json'
import whoo_ABI from './whoo.json'
import { ChainId, getENVChainId } from '../../constants'

const wethABIs = 
{
   [ChainId.HTNET]: wht_ABI,

   [ChainId.BSCNET]: wbnb_ABI,

   [ChainId.HSCNET]: whoo_ABI,
}

export default function getWETHABI(chainId? : ChainId)
{
   return chainId ? wethABIs[chainId] : wethABIs[getENVChainId()]
}