/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit'
import getSyruppools from 'sdk/syruppools'

import { fetchPoolsBlockLimits, fetchPoolsTotalStaking } from './fetchPools'
import {
  fetchPoolsAllowance,
  fetchUserBalances,
  fetchUserStakeBalances,
  fetchUserPendingRewards,
  fetchPoolsAllowanceById,
  fetchUserDataById
} from './fetchPoolsUser'

import { SyrupPoolsState, SyrupPool } from '../types'

const initialState: SyrupPoolsState = { data: [...getSyruppools()] }

export const syruppoolsSlice = createSlice({
  name: 'syruppools',
  initialState,
  reducers: {
    setPoolsPublicData: (state, action) => {
      const livePoolsData: SyrupPool[] = action.payload
      state.data = state.data.map((pool) => {
        const livePoolData = livePoolsData.find((entry) => entry.syrupId === pool.syrupId)
        return { ...pool, ...livePoolData }
      })
    },
    
    setPoolsUserData: (state, action) => {
      const userData = action.payload
      state.data = state.data.map((pool) => {
        const userPoolData = userData.find((entry) => entry.syrupId === pool.syrupId)
        return { ...pool, userData: userPoolData }
      })
    },

    updatePoolsUserData: (state, action) => {
      const { field, value, syrupId } = action.payload
      const index = state.data.findIndex((p) => p.syrupId === syrupId)
      state.data[index] = { ...state.data[index], userData: { ...state.data[index].userData, [field]: value } }
    },
  },
})

// Actions
export const { setPoolsPublicData, setPoolsUserData, updatePoolsUserData } = syruppoolsSlice.actions

// Thunks
export const fetchPoolsPublicDataAsync = () => async (dispatch) => {
  const blockLimits = await fetchPoolsBlockLimits()
  const totalStakings = await fetchPoolsTotalStaking()

  const liveData = getSyruppools().map((pool) => {
    const blockLimit = blockLimits.find((entry) => entry.syrupId === pool.syrupId)
    const totalStaking = totalStakings.find((entry) => entry.syrupId === pool.syrupId)
    return {
      ...blockLimit,
      ...totalStaking,
    }
  })

  dispatch(setPoolsPublicData(liveData))
}

export const fetchPoolsUserDataAsync = (account) => async (dispatch) => {
  const allowances = await fetchPoolsAllowance(account)

  const stakingTokenBalances = await fetchUserBalances(account)

  const stakedBalances = await fetchUserStakeBalances(account)

  const pendingRewards = await fetchUserPendingRewards(account)

  const userData = getSyruppools().map((pool) => ({
    syrupId: pool.syrupId,

    allowance: allowances[pool.syrupId],

    stakingTokenBalance: stakingTokenBalances[pool.syrupId],

    stakedBalance: stakedBalances[pool.syrupId],

    pendingReward: pendingRewards[pool.syrupId],
  }))

  dispatch(setPoolsUserData(userData))
}

export const updateUserAllowance = (syrupId: string, account: string) => async (dispatch) => {
  const allowance = await fetchPoolsAllowanceById(syrupId, account)

  dispatch(updatePoolsUserData({ syrupId, field: 'allowance', value: allowance }))
}

export const updateUserData = (syrupId: string, account: string) => async (dispatch) => {
  const result = await fetchUserDataById(syrupId, account)

  dispatch(updatePoolsUserData({ syrupId, field: 'stakedBalance', value: result. stakedBalance}))

  dispatch(updatePoolsUserData({ syrupId, field: 'stakingTokenBalance', value: result. stakingTokenBalance}))

  dispatch(updatePoolsUserData({ syrupId, field: 'pendingReward', value: result. pendingReward}))
}

export default syruppoolsSlice.reducer
