import bsc_price_config from './56'
import ht_price_config from './128'
import hsc_price_config from './70'
import { ChainId, getENVChainId } from '../constants'
import { BenchmarkPriceConfig } from '../types'

const priceLps = 
{
   [ChainId.HTNET]: ht_price_config,

   [ChainId.BSCNET]: bsc_price_config,

   [ChainId.HSCNET]: hsc_price_config
}

export default function getBenchmarkPriceLp(chainId? : ChainId) : BenchmarkPriceConfig
{
   return chainId ? priceLps[chainId] : priceLps[getENVChainId()]
}