/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit'
import getAirdrops from 'sdk/airdrops'
import fetchAirdrops from './fetchAirdrops'
import fetchAirdropsUser from './fetchAirdropsUser'
import { AirdropPoolsState } from '../types'

const initialState: AirdropPoolsState = { data: [...getAirdrops()] }

export const airdroppoolsSlice = createSlice({
  name: 'airdroppools',
  initialState,
  reducers: {
    setPoolsPublicData: (state, action) => {
      const info = action.payload
      state.data = state.data.map((pool) => {
        const livePoolData = info.find((entry) => entry.id === pool.id)
        return { ...pool, info: livePoolData }
      })
    },

    setPoolsUserData: (state, action) => {
      const userData = action.payload
      state.data = state.data.map((pool) => {
        const userPoolData = userData.find((entry) => entry.id === pool.id)
        return { ...pool, userData: userPoolData }
      })
    },
  },
})

// Actions
export const { setPoolsPublicData, setPoolsUserData } = airdroppoolsSlice.actions

// Thunks
export const fetchAirdropsDataAsync = () => async (dispatch) => {
  const results = await fetchAirdrops()

  dispatch(setPoolsPublicData(results))
}

export const fetchAirdropsUserDataAsync = (account) => async (dispatch) => {
  const results = await fetchAirdropsUser(account)

  dispatch(setPoolsUserData(results))
}

export const updateAirdropsUserDataAsync= (account) => async (dispatch) => {
  const publicresults = await fetchAirdrops()

  dispatch(setPoolsPublicData(publicresults))

  const userresults = await fetchAirdropsUser(account)

  dispatch(setPoolsUserData(userresults))
}

export default airdroppoolsSlice.reducer