import htSyruppools from './128'
import hscSyruppools from './70'
import bscSyruppools from './56'
import { ChainId, getENVChainId } from '../constants'

const syruppools = {
  [ChainId.BSCNET]: bscSyruppools,

  [ChainId.HSCNET]: hscSyruppools,

  [ChainId.HTNET]: htSyruppools
}

export default function getSyruppools(chainId? : ChainId)
{
   return chainId ? syruppools[chainId] : syruppools[getENVChainId()]
}