import BigNumber from 'bignumber.js'
import erc20ABI from 'sdk/abis/erc20.json'
import liquiditypoolABI from 'sdk/abis/liquiditypool.json'
import multicall from 'utils/multicall'
import { LiquidityPoolConfig } from 'sdk'
import getLiquiditypools from 'sdk/liquiditypools'

export const fetchFarmUserAllowances = async (account: string, farmsToFetch: LiquidityPoolConfig[]) => {
  const calls = farmsToFetch.map((config) => {
    const liquidityAddress = config.liquidity.address

    const liquidityPoolAddress = config.contractAddress

    return { address: liquidityAddress, name: 'allowance', params: [account, liquidityPoolAddress] }
  })

  const rawLpAllowances = await multicall(erc20ABI, calls)

  const parsedLpAllowances = rawLpAllowances.map((lpBalance) => {
    return new BigNumber(lpBalance).toJSON()
  })

  return parsedLpAllowances
}

export const fetchFarmUserTokenBalances = async (account: string, farmsToFetch: LiquidityPoolConfig[]) => {
  const calls = farmsToFetch.map((config) => {
    const liquidityAddress = config.liquidity.address

    return {
      address: liquidityAddress,
      name: 'balanceOf',
      params: [account],
    }
  })

  const rawTokenBalances = await multicall(erc20ABI, calls)

  const parsedTokenBalances = rawTokenBalances.map((tokenBalance) => {
    return new BigNumber(tokenBalance).toJSON()
  })

  return parsedTokenBalances
}

export const fetchFarmUserStakedBalances = async (account: string, farmsToFetch: LiquidityPoolConfig[]) => {
  const calls = farmsToFetch.map((config) => {
    const liquidityPoolAddress = config.contractAddress

    return {
      address: liquidityPoolAddress,

      name: 'userInfo',

      params: [account],
    }
  })

  const rawStakedBalances = await multicall(liquiditypoolABI, calls)

  const parsedStakedBalances = rawStakedBalances.map((stakedBalance) => {
    return new BigNumber(stakedBalance[0]._hex).toJSON()
  })

  return parsedStakedBalances
}

export const fetchFarmUserEarnings = async (account: string, farmsToFetch: LiquidityPoolConfig[]) => {
  const calls = farmsToFetch.map((config) => {
    const liquidityPoolAddress = config.contractAddress
 
    return {
      address: liquidityPoolAddress,
      name: 'pendingReward',
      params: [account],
    }
  })

  const rawEarnings = await multicall(liquiditypoolABI, calls)

  const parsedEarnings = rawEarnings.map((earnings) => {
    return new BigNumber(earnings).toJSON()
  })

  return parsedEarnings
}

export const fetchFarmUserAllowanceById = async (liquidityid: any, account: string) => {
  const config = getLiquiditypools().find((p) => p.liquidityid === liquidityid)
 
  const calls = [{
    address: config.liquidity.address, 
    
    name: 'allowance', 
    
    params: [account, config.contractAddress] 
  }]

  const allowance = await multicall(erc20ABI, calls)

  return new BigNumber(allowance).toJSON()
}


export const fetchUserDataById = async (liquidityid, account) => {
  const config = getLiquiditypools().find((p) => p.liquidityid === liquidityid)

  const calls = [{
    address: config.liquidity.address,

    name: 'balanceOf',
    
    params: [account],
  }]

  const [tokenBalance] = await multicall(erc20ABI, calls)

   const poolcalls =[{
    address: config.contractAddress,

    name: 'userInfo',
    
    params: [account],
  },

  {
    address: config.contractAddress,

    name: 'pendingReward',

    params: [account],
  }]

  const [userInfo, pendingReward] = await multicall(liquiditypoolABI, poolcalls)

  return {
    stakingTokenBalance: new BigNumber(tokenBalance).toJSON(),

    stakedBalance: new BigNumber(userInfo.amount._hex).toJSON(),

    pendingReward: new BigNumber(pendingReward).toJSON()
  }
}
