import React from 'react'
import { Card, CardBody, Heading, Text } from 'uikit'
import styled from 'styled-components'
import { getBalanceNumber } from 'utils/formatBalance'
import { useTotalSupply, useBurnedBalance, useGrausPrice } from 'hooks/useTokenBalance'
import { useTranslation } from 'contexts/Localization'
import { ChainId, getChainName } from 'sdk'
import { getGrausAddress, getGrausDecimals } from 'utils/addressHelpers'
import CardValue from './CardValue'

const StyledCakeStats = styled(Card)`
  margin-left: auto;
  margin-right: auto;
`

const Row = styled.div`
  align-items: center;
  display: flex;
  font-size: 14px;
  justify-content: space-between;
  margin-bottom: 8px;
`

export interface Props {
  chainId: ChainId
}

const GrausStats: React.FC<Props> = ({
  chainId
}) => {
  const { t } = useTranslation()

  const totalSupply = useTotalSupply(chainId)
 
  const burnedBalance = getBalanceNumber(useBurnedBalance(getGrausAddress(chainId), chainId), getGrausDecimals(chainId))
  
  const grausSupply = totalSupply ? getBalanceNumber(totalSupply, getGrausDecimals(chainId)) - burnedBalance : 0
 
  const initSupplyy = totalSupply ? getBalanceNumber(totalSupply, getGrausDecimals(chainId))  : 0

  const price = useGrausPrice(chainId)

  return (
    <StyledCakeStats>
      <CardBody>
        <Heading scale="md" mb="24px">
          {t(getChainName(chainId))} - {t('tokenStats', { name: "Graus"})} 
        </Heading>
        <Row>
          <Text fontSize="14px">{t('totalSupply')}</Text>
          {initSupplyy && <CardValue fontSize="14px" value={initSupplyy} />}
        </Row>
        <Row>
          <Text fontSize="14px">{t('circleAmount')}</Text>
          {grausSupply && <CardValue fontSize="14px" value={grausSupply} />}
        </Row>
        <Row>
          <Text fontSize="14px">{t('totalBurned')}</Text>
          <CardValue fontSize="14px" decimals={0} value={burnedBalance} />
        </Row>

        <Row>
          <Text fontSize="14px">{t('marketcap')}</Text>
          <CardValue fontSize="14px" decimals={2} value={grausSupply * price} prefix='$' />
        </Row>
      </CardBody>
    </StyledCakeStats>
  )
}

export default GrausStats
