import React, { useMemo } from 'react'
import FarmsPageHeader from 'components/FarmsPageHeader'
import Page from 'components/layout/Page'
import { useTranslation } from 'contexts/Localization'
import { useActiveWeb3React } from 'hooks'
import { Heading, Flex } from 'uikit'
import orderBy from 'lodash/orderBy'
import partition from 'lodash/partition'
import usePersistState from 'hooks/usePersistState'
import { useAirdropPools } from 'state/hooks'
import FlexLayout from 'components/layout/Flex'
import AirdroppoolTab from './AirdroppoolTab'
import Airdroppoolcard from './Airdroppoolcard'

const Syruppools: React.FC = () => {
    const { t } = useTranslation()

    const { account } = useActiveWeb3React()

    const pools = useAirdropPools(account)

    const [showFinished, setShowFinished] = usePersistState(false, 'grampus_airdrop_pool_finished')

    const [showRewarded, setShowRewarded] = usePersistState(false, 'grampus_airdrop_pool_rewarded')

    const [finishedPools, livePools] = useMemo(
      () => partition(pools, (pool) => pool.info.airdropCount === pool.info.hasAirdropCount),
      [pools],
    )

    const rewardedFinishedPools = useMemo(
      () => finishedPools.filter((pool) => pool.userData && (pool.userData.userAirdropAmount > 0 || pool.userData.userInviterCount > 0)),
      [finishedPools],
    )

    const rewardedLivePools = useMemo(
      () => livePools.filter((pool) => pool.userData && (pool.userData.userAirdropAmount > 0 || pool.userData.userInviterCount > 0)),
      [livePools],
    )

    const hasRewardedInFinishedPools = rewardedFinishedPools.length > 0

    return (
      <>
        <FarmsPageHeader>
         <Flex justifyContent="space-between" flexDirection={['column', null, 'row']}>
            <Flex flexDirection="column" mr={['8px', 0]}>
              <Heading as="h1" scale="xxl" color="secondary" mb="24px">
                {t('Airdrop Pools')}
              </Heading>
              <Heading scale="md" color="text">
                {t('earn random amount of airdrop tokens')}
              </Heading>
            </Flex>
          </Flex>
        </FarmsPageHeader>
        <Page>

        <AirdroppoolTab
          showFinished={showFinished}
          setShowFinished={setShowFinished}
          showRewarded={showRewarded}
          setShowRewarded={setShowRewarded}
          hasRewardedInFinishedPools={hasRewardedInFinishedPools}
        />
        { !showFinished ? <FlexLayout>
            <> 
              {showRewarded
                ? orderBy(rewardedLivePools, ['sortOrder']).map((pool) => (
                    <Airdroppoolcard key={pool.id} pool={pool} account={account} />
                  ))
                : orderBy(livePools, ['sortOrder']).map((pool) => (
                    <Airdroppoolcard key={pool.id} pool={pool} account={account} />
                  ))}
            </>
        </FlexLayout>
        : <FlexLayout> 
          <> 
          {showRewarded
              ? orderBy(rewardedFinishedPools, ['sortOrder']).map((pool) => (
                  <Airdroppoolcard key={pool.id} pool={pool} account={account} />
                ))
              : orderBy(finishedPools, ['sortOrder']).map((pool) => (
                  <Airdroppoolcard key={pool.id} pool={pool} account={account} />
                ))} 
           </>
        </FlexLayout> }
      </Page>
      </>
    )   
}
 
export default Syruppools
