import { ConnectorNames } from 'uikit'
import Web3 from 'web3'
import { Web3Provider } from '@ethersproject/providers'
import { InjectedConnector } from '@web3-react/injected-connector'
import { WalletConnectConnector } from '@web3-react/walletconnect-connector'
import { BscConnector } from '@binance-chain/bsc-connector'
import { ChainId, getNodeUrl, getENVChainId } from 'sdk'
import { NetworkConnector } from './NetworkConnector'

const networks = 
{
   [ChainId.HSCNET] : new NetworkConnector({
    urls: { [ChainId.HSCNET]: getNodeUrl(ChainId.HSCNET)},
  
    defaultChainId: ChainId.HSCNET
  }),

  [ChainId.HTNET] : new NetworkConnector({
    urls: { [ChainId.HTNET]: getNodeUrl(ChainId.HTNET)},
  
    defaultChainId: ChainId.HTNET
  }),
}

export function getNetwork(chainId? : ChainId){
  return chainId ? networks[chainId] : networks[getENVChainId()]
}

let networkLibrary: Web3Provider | undefined

export function getNetworkLibrary(): Web3Provider {
  // eslint-disable-next-line no-return-assign
  return (networkLibrary = networkLibrary ?? new Web3Provider(getNetwork().provider as any))
}

const injecteds = {
  [ChainId.HSCNET] : new InjectedConnector({
    supportedChainIds: [ChainId.HSCNET],
  }),

  [ChainId.HTNET] : new InjectedConnector({
    supportedChainIds: [ChainId.HTNET],
  }),
}

const bscConnectors = {
  [ChainId.HSCNET]: new BscConnector({ supportedChainIds: [ChainId.HSCNET] }),

  [ChainId.HTNET]: new BscConnector({ supportedChainIds: [ChainId.HTNET] }),
}

// mainnet only
const walletconnects = {
  [ChainId.HTNET] : new WalletConnectConnector({
    rpc: {  [ChainId.HTNET]: getNodeUrl(ChainId.HTNET)},
  
    bridge: 'https://bridge.walletconnect.org',
  
    qrcode: true,
  
    pollingInterval: 15000,
  }),

  [ChainId.HSCNET] : new WalletConnectConnector({
    rpc: {  [ChainId.HSCNET]: getNodeUrl(ChainId.HSCNET)},
  
    bridge: 'https://bridge.walletconnect.org',
  
    qrcode: true,
  
    pollingInterval: 15000,
  }),
}

// mainnet only
const ConnectorsByName: { [connectorName in ConnectorNames]: any } = {
  [ConnectorNames.Injected]: injecteds,

  [ConnectorNames.WalletConnect]: walletconnects,

  [ConnectorNames.BSC]: bscConnectors,
}

export function getConnectorsByName(connectorName: ConnectorNames, chainId? : ChainId)
{
   return chainId? ConnectorsByName[connectorName][chainId]: ConnectorsByName[connectorName][getENVChainId()]
}

export const getLibrary = (provider): Web3 => {
  return provider
}
