import { getNftMarket } from 'sdk/nft/shop'
import marketABI from 'sdk/nft/shop/marketABI.json'
import erc721ABI from 'sdk/abis/erc721.json'
import multicall from 'utils/multicall'
import { ADDRESS0 } from 'sdk'

export const fetchAllowance = async (account, nft, tokenid) => {
  const market = getNftMarket()

  const shop = market.shops.find((item) => (item.nft === nft)) 

  const calls = [{
     address: nft,

     name: 'getApproved',
    
     params: [tokenid],
  },

  {
    address: nft,

    name: 'ownerOf',
   
    params: [tokenid],
  },

  {
    address: nft,

    name: 'isApprovedForAll',
    
    params: [account, shop?.address || ADDRESS0],
  }]

  const [approved, _owner, isAll] = await multicall(erc721ABI, calls)

  return {
    isApproved: approved === shop?.address || isAll[0],

    owner: _owner[0]
  }
}

export const fetchFeePercent = async() => {
    const market = getNftMarket()

    const calls = [{
      address: market.address,

      name: 'getFeePercent',
    }]
  
   const feePercent = await multicall(marketABI, calls) 
  
   return feePercent.toString()
}
 

 