import React, { useState } from 'react'
import { parseUnits } from '@ethersproject/units'
import styled from 'styled-components'
import Select from 'react-select'
import { Button, Modal, Flex, Text } from 'uikit'
import { getETHId, ADDRESS0 } from 'sdk'
import { getNftPayTokens } from 'sdk/nft/shop'
import { onPriceAmountKeyDown } from 'utils/checkInput'
import { useTranslation } from 'contexts/Localization'
import useNftSellAsk from 'hooks/useNftSellAsk'
import { InputLabel } from 'components/Label'
import { useNftForSell, useGetApiPrices } from 'state/hooks'
 
const StyledInput = styled.input`
  width: 200px;  
  position: relative;
  display: flex;
  padding: 5px;
  margin-right: 5px;
  align-items: center;
  white-space: nowrap;
  text-align: right;
  background: none;
  border: none;
  outline: none;
  border-radius: 12px;
  color: ${({ theme }) => theme.colors.text};
  border-style: solid;
  border: 1px solid ${({ theme }) => theme.colors.primary};
  -webkit-appearance: none;

  font-size: 12px;

  ::placeholder {
    color: ${({ theme }) => theme.colors.textDisabled};
  }
  transition: border 100ms;
  :focus {
    outline: none;
  }
`

const CaptionContainer = styled.div`
  min-height: 24px;
  display: flex;
  align-items: center;
`

const ContentContainer = styled.div`
  min-height: 24px;
  display: flex;
  align-items: center;
`

interface NftForSellAskModalProps {
  nft: string,

  tokenid: string,

  onDismiss?: () => void,

  onSuccess?: () => void
}

const NftForSellAskModal: React.FC<NftForSellAskModalProps> = ({ nft, tokenid, onDismiss, onSuccess }) => {
  const [pendingTx, setPendingTx] = useState(false)

  const [commitResult, setCommitResult] = useState({ step: 'step 1', message: 'waiting for inpur price and pay token', success: true })

  const { t } = useTranslation()

  const nftForSell = useNftForSell(nft, tokenid)

  const paytokens = getNftPayTokens()

  const prices = useGetApiPrices()

  const { ethPrice } = prices

  const options = [{
    label: getETHId(),

    value: {
      address: ADDRESS0,

      price: ethPrice,

      decimals: 18
    }
  }].concat(paytokens.map((token) =>
  {
     return {
       label: token.symbol,

       value: {
         address: token.address,

         price: prices[token.address],

         decimals: token.decimals
       }
     }
  }))

  const [selectPayToken, setSelectPayToken] = useState(options[0])

  const [priceText, setPriceText] = useState(0)

  function getPriceDollar(p, s)
  {
    const priceDollar = p * s.value.price

    const isHighValue = Math.round(priceDollar * 1000) < 1

    return priceDollar.toFixed(isHighValue ? 4 : 2)
  }

  const [priceDollarText, setPriceDollarText] = useState(getPriceDollar(priceText, selectPayToken))

  const selectStyles = {
    container: styles => ({ ...styles,  width: 100 })
  }

  const onPriceChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const price = Number.parseFloat(e.target.value)
    
    setPriceDollar(Number.isNaN(price) ? 0 : price, selectPayToken)
   
    setPriceText(Number.isNaN(price) ? 0 : price)
  }

  const handleChange = (selected) => {
    if(selected !== selectPayToken)
    {
       setPriceDollar(priceText, selected)

       setSelectPayToken(selected)
    }
  }

  function setPriceDollar(p, s)
  {
     setPriceDollarText(getPriceDollar(p, s))
  }

  const onPriceKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) =>
  {
    onPriceAmountKeyDown(e, selectPayToken.value.decimals)
  }

  const askCallback = () =>
  {
    setPendingTx(false)

    setCommitResult({
      success: true,

      step: "end",

      message: "success"
    })

    if(onSuccess)
    {
      onSuccess()
    }

    onDismiss()
  }

  const [ onAsk ] = useNftSellAsk(nft, tokenid, selectPayToken.value.address, parseUnits(priceText.toString(), selectPayToken.value.decimals || 18).toString(), askCallback)

  const handleOnAsk = async () => {
      try {
        if(priceText <= 0 || Number.isNaN(priceText))
        {
          setCommitResult({
            success: false,
      
            step: "step 2",
      
            message: t('price must large than 0')
          }) 
        }

        else
        {
         setPendingTx(true)

         await onAsk()
        }

      } catch (e) {
        setPendingTx(false)

        setCommitResult({
          success: false,
    
          step: "step 1",
    
          message: e.message
        }) 
      }
  }

  return (
    <Modal title={t('Commit')} onDismiss={onDismiss} >
      <Flex maxWidth='420px' minWidth='420px' alignItems="center" flexDirection="column" justifyContent="space-between" mb="8px">
        <Text bold color="textSubtle" fontSize="18px" pr="3px">
         {t(commitResult.step)}
        </Text>
        { commitResult.success ? (<Text color="textSubtle" fontSize="14px" pr="3px" mt='10px'>
         {t(commitResult.message)}
        </Text>) :
        (<Text bold color="failure" fontSize="14px" pr="3px" mt='10px'>
         {t(commitResult.message)} 
        </Text> )}
      </Flex>

      <Flex flexDirection="row" mt='10px' mb='5px' justifyContent="space-between">
        <CaptionContainer>
         <InputLabel text={t('Price')} isNecessary/>
        </CaptionContainer>
        <ContentContainer>
           <Flex flexDirection="row">
           <StyledInput defaultValue='0' onKeyDown={onPriceKeyDown} placeholder={t('')} onChange={onPriceChange} type='text'/>
           <Select styles={selectStyles} value={selectPayToken} onChange={handleChange} options={options}/>
           </Flex>
        </ContentContainer>
       </Flex>

       <Flex flexDirection="row" mt='5px' mb='5px' justifyContent="space-between">
         <CaptionContainer>
           <InputLabel text='' />
         </CaptionContainer>
        <ContentContainer>
         <Text fontSize="14px" ml='12px' color="textSubtle">
          {priceDollarText} $
         </Text>
        </ContentContainer>
       </Flex>

       <Flex flexDirection="row" mt='5px' mb='10px' justifyContent="space-between">
        <CaptionContainer>
         <InputLabel text={t('Trade Fee')} />
        </CaptionContainer>

        <ContentContainer>
        <Text fontSize="14px" ml='12px' color="textSubtle">
         {`${nftForSell?.feePercent}%`}
        </Text>
        </ContentContainer>
       </Flex>

      <Button
        isLoading={pendingTx}
        onClick={handleOnAsk}
        mt="24px"
      >
        {pendingTx ? t('Committing') : t('Commit')}
      </Button>
    </Modal>
  )
}

export default NftForSellAskModal
