import OSS from 'ali-oss'

const ossClient = new OSS(
{
    region: 'oss-cn-shanghai',

    accessKeyId: 'LTAI5tFEiV3V8u3nzctkPBv2',

    accessKeySecret: 'kFr53dgGcSfRupgSitAvZd6aKoWSRp',

    bucket: 'jsnft',

    secure: true
})

export const uploadFile = async (url: string, file: any, callback: (success: boolean, error?: string, result?: any) => void) =>
{
    try {
        const reader = new FileReader()

        reader.readAsDataURL(file)

        await ossClient.multipartUpload(url, file)

        callback(true)      
      } catch (e) {
      callback(false, e.message)
     }
}

export const uploadBuffer = async (url: string, file: Buffer, callback: (success: boolean, error?: string, result?: any) => void) =>
{
    try {
      await ossClient.put(url, file)

      callback(true)      
      } catch (e) {
      callback(false, e.message)
     }
}

export const downloadBuffer = async (url: string) =>
{
    try {
        const result = await ossClient.get(url)

        const jsonResult = JSON.parse(result.res.data.toString())

        return jsonResult  
      } catch(e) {
        return undefined
    }
}

export const parseFullUrl = (url: string) =>
{
   return `https://jsnft.oss-cn-shanghai.aliyuncs.com${url}`
}

 