import pair_ABI from './mdexpair.json'
import uni_pair_ABI from './IUniswapV2Pair.json'
 
import { ChainId, getENVChainId } from '../../constants'

const pairABIs = 
{
   [ChainId.HTNET]: pair_ABI,

   [ChainId.BSCNET]: uni_pair_ABI,

   [ChainId.HSCNET]: uni_pair_ABI
}

export default function getPairABI(chainId? : ChainId)
{
   return chainId ? pairABIs[chainId] : pairABIs[getENVChainId()]
}