import React, { useState } from 'react'
import styled from 'styled-components'
import { useTranslation } from 'contexts/Localization'
import { Flex, CardFooter, ExpandableLabel } from 'uikit'
import { AirdropPool } from 'state/types'
import { ManualPoolTag } from 'components/Tags'
import ExpandedFooter from './ExpandedFooter'

interface FooterProps {
  pool: AirdropPool,
  account:string
}

const ExpandableButtonWrapper = styled(Flex)`
  align-items: center;
  justify-content: space-between;
  button {
    padding: 0;
  }
`

const Footer: React.FC<FooterProps> = ({
  pool, account
}) => {
  const { t } = useTranslation()
  
  const [isExpanded, setIsExpanded] = useState(false)

  return (
    <CardFooter>
      <ExpandableButtonWrapper>
        <Flex alignItems="center">
          <ManualPoolTag />
        </Flex>
        <ExpandableLabel expanded={isExpanded} onClick={() => setIsExpanded(!isExpanded)}>
          {isExpanded ? t('Hide') : t('Details')}
        </ExpandableLabel>
      </ExpandableButtonWrapper>
      {isExpanded && (
        <ExpandedFooter
          rewardToken={pool.rewardToken}
          airdropCount={pool.info?.airdropCount}
          hasAirdropCount={pool.info?.hasAirdropCount}
          hasAirdropAmount={pool.info?.hasAirdropAmount}
          account={account}
        />
      )}
    </CardFooter>
  )
}

export default Footer
