import JSBI from 'jsbi'

export type BigintIsh = JSBI | bigint | string

export enum ChainId {
    BSCNET = 56,

    HTNET = 128,

    HSCNET = 70,
    
    BSCTESTNET = 97
  }

export enum TokenType
{
    ETH = 1,

    USD = 2,

    Token = 3
}

  export enum TradeType {
    EXACT_INPUT,

    EXACT_OUTPUT
  }
  
  export enum Rounding {
    ROUND_DOWN,

    ROUND_HALF_UP,

    ROUND_UP
  }

  export enum SolidityType 
  {
    uint8 = 'uint8',
  
    uint256 = 'uint256'
  }

  export const supportChainIds = [ChainId.HSCNET, ChainId.HTNET]

  const chainIdLocalStorageKey = 'grampus_chain_id_key'

  const value = window.localStorage.getItem(chainIdLocalStorageKey);

  if(!value)
  {
    setENVChainId(parseInt(process.env.REACT_APP_CHAIN_ID, 10) ?? ChainId.HTNET)
  }

  export function getENVChainId()
  {
    return parseInt(window.localStorage.getItem(chainIdLocalStorageKey))
  }

  export function setENVChainId(chainId: ChainId)
  {
    window.localStorage.setItem(chainIdLocalStorageKey, chainId.toString())
  }

