import htAirdrops from './128'
import hscAirdrops from './70'
import bscAirdrops from './56'
import { ChainId, getENVChainId } from '../constants'

const airdrops = {
  [ChainId.BSCNET]: bscAirdrops,

  [ChainId.HTNET]: htAirdrops,

  [ChainId.HSCNET]: hscAirdrops
}

export default function getAirdrops(chainId? : ChainId)
{
   return chainId ? airdrops[chainId] : airdrops[getENVChainId()]
}