import React from 'react'
import {
  ButtonMenu,
  ButtonMenuItem,
  Toggle,
  Text,
  Flex,
  NotificationDot,
} from 'uikit'
import { useTranslation } from 'contexts/Localization'

const AirdroppoolTab = ({ showFinished, setShowFinished, showRewarded, setShowRewarded, hasRewardedInFinishedPools }) => {
  const { t } = useTranslation()

  return (
    <Flex alignItems="center" justifyContent="center" mb="32px">
      <Flex alignItems="center" flexDirection={['column', null, 'row', null]}>
        <ButtonMenu activeIndex={showFinished ? 1 : 0} onItemClick={(index) => setShowFinished(index === 1)} scale="sm" variant="subtle">
          <ButtonMenuItem>
            {t('Live')}
          </ButtonMenuItem>

          <NotificationDot show={hasRewardedInFinishedPools}>
            <ButtonMenuItem>
               {t('Finished')}
            </ButtonMenuItem>
          </NotificationDot>
        </ButtonMenu>

        <Flex mt={['4px', null, 0, null]} ml={[0, null, '24px', null]} justifyContent="center" alignItems="center">
          <Toggle scale="sm" checked={showRewarded} onChange={() => setShowRewarded((prev) => !prev)} />
          <Text ml="8px">{t('Rewarded only')}</Text>
        </Flex>
      </Flex>
    </Flex>
  )
}

export default AirdroppoolTab
