import { useCallback } from 'react'
import { useAppDispatch } from 'state'
import { useActiveWeb3React } from 'hooks'
import { fetchOGUserNftsAsync, setOnSellPageQCVersion } from 'state/actions'
import { useNftShopContract } from './useContract'
import { calculateGasMargin } from '../utils'   

export default function useNftSellCancelAsk(
  shop: string, tokenid: string, callback: any
): [ () => Promise<void>] {
  const { account } = useActiveWeb3React()

  const dispatch = useAppDispatch()

  const shopContract = useNftShopContract(shop)
  
  const onCancelAsk = useCallback(async (): Promise<void> => {
    if (!shopContract) {
      console.error('nft shop contract is null')

      return
    }

    const estimatedGas = await shopContract.estimateGas.cancelAsk(tokenid)
                                                      
    // eslint-disable-next-line consistent-return
    shopContract
      .cancelAsk(tokenid, { gasLimit: calculateGasMargin(estimatedGas) })
      .then(() => {
                const interval = setInterval(async () => {
                  dispatch(setOnSellPageQCVersion())
                  
                  dispatch(fetchOGUserNftsAsync(account)).then(
                    () =>
                    {
                     clearInterval(interval)   
  
                     callback()     
                    }
                   )
                }, 6000)   
        })
        .catch((error: Error) => {
          console.error('Failed to cancel ask nft', error)
          throw error
        })
  }, [shopContract, dispatch, account, callback, tokenid])

  return [ onCancelAsk ]
}

 
