import BigNumber from 'bignumber.js'
import { TokenType, getENVChainId } from 'sdk'
import getLiquidities from 'sdk/liquidities'
import getBenchmarkPriceLp from 'sdk/benchmarkprices'
import getTokens from 'sdk/tokens'
import { getBalanceNumber } from 'utils/formatBalance'
import erc20ABI from 'sdk/abis/erc20.json'
import multicall from 'utils/multicall'

const fetchPrices = async () => {
  const liquidities = getLiquidities()

  const chainId = getENVChainId()

  const config = getBenchmarkPriceLp(chainId)

  const ethcalls = [
    // Balance of token in the LP contract
    {
      address: config.usd.address,

      name: 'balanceOf',
      
      params: [config.ethlpaddress],
    },

    // Balance of quote token on LP contract
    {
      address: config.eth.address,

      name: 'balanceOf',
      
      params: [config.ethlpaddress],
    }
  ]

  const [
    usd,

    eth
  ] = await multicall(erc20ABI, ethcalls, chainId) 

  const _ethPrice =
    getBalanceNumber(new BigNumber(usd), config.usd.decimals) /
    getBalanceNumber(new BigNumber(eth), config.eth.decimals)

    const data = { ethPrice: _ethPrice }

    const tokens = getTokens()

    const tokenKeys = Object.keys(tokens)

    tokenKeys.forEach((address)=>
    { 
     const token = tokens[address]

     if(token.type === TokenType.ETH)
     {
      data[token.address] = _ethPrice
     }
   })

   const liquidityKeys = Object.keys(liquidities)

  await Promise.all(
    liquidityKeys.map(async (lpley) => {
      const liquidity = liquidities[lpley]

      const calls = [
        // Balance of token in the LP contract
        {
          address: liquidity.token.address,

          name: 'balanceOf',
          
          params: [liquidity.address],
        },

        // Balance of quote token on LP contract
        {
          address: liquidity.quoteToken.address,

          name: 'balanceOf',
          
          params: [liquidity.address],
        },
      ]

      const [
        tokenAmount,

        quoteTokenAmount
      ] = await multicall(erc20ABI, calls)

      const tokenPrice = liquidity.quoteToken.type === TokenType.ETH ?  
        ( getBalanceNumber(new BigNumber(quoteTokenAmount), liquidity.quoteToken.decimals) /
          getBalanceNumber(new BigNumber(tokenAmount), liquidity.token.decimals) * _ethPrice) :
        ( getBalanceNumber(new BigNumber(quoteTokenAmount), liquidity.quoteToken.decimals) /
          getBalanceNumber(new BigNumber(tokenAmount), liquidity.token.decimals))

      data[liquidity.token.address] = tokenPrice
      
    }),
  )

  return data
}

export default fetchPrices 
