import React from 'react'
import { CardBody, Flex, Text, CardRibbon } from 'uikit'
import UnlockButton from 'components/UnlockButton'
import { useTranslation } from 'contexts/Localization'
import { AirdropPool } from 'state/types'
import { StyledCard, StyledCardInner } from './StyledCard'
import StyledCardHeader from './StyledCardHeader'
import AirdropInfoRow from './AirdropInfoRow'
import AirdropEarnRow from './AirdropEarnRow'
import CardFooter from './CardFooter/Footer'
import CardActions from './CardActions/CardActions'

const Airdroppoolcard: React.FC<{ pool: AirdropPool; account: string; }> = ({ pool, account }) => {
  const { rewardToken, info, userData } = pool

  const isFinished = info && info.airdropCount === info.hasAirdropCount

  const isRewarded = userData && userData.userAirdropAmount > 0

  const { t } = useTranslation()

  return (
    <StyledCard
      isFinished={isFinished}
      ribbon={isFinished && <CardRibbon variantColor="textDisabled" text={`${t('Finished')}`} />}
    >
      <StyledCardInner>
        <StyledCardHeader
          isRewarded={isRewarded}
          rewardTokenSymbol={rewardToken.symbol}
          isFinished={isFinished}
        />

       <CardBody>
        <AirdropInfoRow    
           airdropCount={info.airdropCount}
           baseAirdropAmount={info.baseAirdropAmount} />

        <AirdropEarnRow 
          inviteRewardRatio={info.inviteRewardRatio}
          userAirdropAmount={userData?.userAirdropAmount}
          userInviterAmount={userData?.userInviterAmount}
          userInviterCount={userData?.userInviterCount}/>

         <Flex mt="24px" flexDirection="column">
            {account ? (
              <CardActions pool={pool} />
            ) : (
              <>
                <Text mb="10px" textTransform="uppercase" fontSize="12px" color="textSubtle" bold>
                  {t('Start Earning')}
                </Text>
                <UnlockButton />
              </>
            )}
          </Flex>
        </CardBody>

        <CardFooter pool={pool} account={account}/>   
      </StyledCardInner>
    </StyledCard>
  )
}

export default Airdroppoolcard
