import React, { useState, useCallback, useEffect } from 'react'
import styled from 'styled-components'
import { Button, useModal } from 'uikit'
import { getNftMarket } from 'sdk/nft/shop'
import { useTranslation } from 'contexts/Localization'
import useToast from 'hooks/useToast'
import useNftSellAskApprove from 'hooks/useNftSellAskApprove'
import { ApprovalState } from 'hooks/useApproveCallback'
import { useAppDispatch } from 'state'
import { useNftForSell } from 'state/hooks'
import { fetchNftForSellAsync } from 'state/actions'
import NftForSellAskModal from './NftForSellAskModal'

const Action = styled.div`
  padding-top: 16px;
`
interface NftForSellActionsProps 
{
  account: string,

  nft: string,

  tokenid: string,

  onCommitSuccess: () => void,
}

const NftForSellActions: React.FC<NftForSellActionsProps> = ({ account, nft, tokenid, onCommitSuccess }) => {
  const { t } = useTranslation()

  const [requestedApproval, setRequestedApproval] = useState(false)

  const nftForSell = useNftForSell(nft, tokenid)

  const [isloading, setIsLoading] = useState(!nftForSell || nftForSell.loading)

  const dispatch = useAppDispatch()

  useEffect(() => {
    if (!nftForSell)
    {  
       dispatch(fetchNftForSellAsync(account, nft, tokenid))
      .then(()=>
       {
        setIsLoading(false)
       })
     }
   }, [dispatch, nftForSell, account, nft, tokenid, setIsLoading])

  const { isApproved, owner } = nftForSell || { }
  
  const { toastSuccess, toastError } = useToast()

  const approveCallback = () =>
  {
    toastSuccess(`${t('Contract Enabled')}`, `${t(`Now, you can sell your nft!`)}`)
       
    setRequestedApproval(false)
  }

  const market = getNftMarket()

  const shop = market.shops.find((item) => (item.nft === nft))

  const [ approveState, onApprove ] = useNftSellAskApprove(nft, shop?.address, tokenid, approveCallback)

  const handleApprove = useCallback(async () => {
    try {
      setRequestedApproval(true)

      await onApprove()

      if (approveState ===  ApprovalState.UNKNOWN) {
        // user rejected tx or didn't go thru
        toastError(
          `${t('Error')}`,
          `${t(`Please try again. Confirm the transaction and make sure you are paying enough gas!`)}`,
        )

        setRequestedApproval(false)
      }  else if (approveState === ApprovalState.APPROVED)
      {
        toastSuccess(`${t('Contract Enabled')}`, `${t(`Now, you can sell your nft!`)}`)
       
        setRequestedApproval(false)
      }

    } catch (e) {
      console.error(e)
    }
  }, [onApprove, approveState, toastSuccess, toastError, t])

  const [onPresentAskModal] = useModal(<NftForSellAskModal nft={nft} tokenid={tokenid} onSuccess={onCommitSuccess}/>)

  const onPresentCreate = () =>
  {
    onPresentAskModal()
  }

  const renderApprovalOrCreateButton = () => {
    return isApproved ? (
      <Button mt="8px" width="100%"
        onClick={onPresentCreate}>
        {t('Sell')}
      </Button>
    ) : (
      <Button mt="8px" width="100%" disabled={requestedApproval} onClick={handleApprove}>
        {t('Enable')}
      </Button>
    )
  }

  return (
    <Action>
      { (!isloading && account && account === owner) ? renderApprovalOrCreateButton() : <></> }
    </Action>
  )
}

export default NftForSellActions
