import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import { RouteComponentProps } from 'react-router'
import { Button, Heading, Text, LogoIcon } from 'uikit'
import { useAppDispatch } from 'state'
import PageLoader from 'components/PageLoader'
import { useTranslation } from 'contexts/Localization'
import { useNftOnSellPreview, useNftMeta, useNftOnsellPageQC } from 'state/hooks'
import { fetchAskByIdAsync, fetchNftMetaAsync } from 'state/actions'
import NftOnSellPreview from "./NftOnSellPreview"
import history from '../../routerHistory'

const StyledNotFound = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;
  height: calc(100vh - 64px);
  justify-content: center;
`
export default function NftOnSellPreviewAgent({
    match: {
      params: { nft, tokenid },
    },
  }: RouteComponentProps<{ nft: string; tokenid: string }>) {
   
   const { t } = useTranslation()

   const previewNode = useNftOnSellPreview(nft, tokenid) 

   const qc = useNftOnsellPageQC()

   const [isPreviewLoading, setPreviewLoading] = useState(!previewNode || (!previewNode.paytoken && !previewNode.loading))

   const dispatch = useAppDispatch()

   useEffect(() => {
    if (!previewNode || (!previewNode.paytoken && !previewNode.loading))
    {  
      dispatch(fetchAskByIdAsync(nft, qc.version, tokenid))
     .then(()=>
      {
        setPreviewLoading(false)
      })
    }
  }, [dispatch, previewNode, nft, qc, tokenid, setPreviewLoading])

   const nftModeMeta = useNftMeta(nft, tokenid)

   const [isMetaLoading, setMetaLoading] = useState(!nftModeMeta || (!nftModeMeta.meta && !nftModeMeta.loading))

   useEffect(() => {
    if (!nftModeMeta || (!nftModeMeta.meta && !nftModeMeta.loading))
    {  
      dispatch(fetchNftMetaAsync(nft, tokenid, nftModeMeta?.uri))
     .then(()=>
      {
        setMetaLoading(false)
      })
    }
  }, [dispatch, nftModeMeta, nft, tokenid, setMetaLoading])

   function onCommitSuccess()
   {
     history.push(`/nftsell`)
   }

   return (
    isMetaLoading || isPreviewLoading ? ( <PageLoader/>) : ((!previewNode || !nftModeMeta || !previewNode || !previewNode.paytoken) ? ( <StyledNotFound>
      <LogoIcon width="64px" mb="8px" />
      <Heading scale="xxl">404</Heading>
      <Text mb="16px">{t('Oops, nft not found.')}</Text>
      <Button as="a" href="/nftsell" scale="sm">
        {t('Back Sell')}
      </Button>
    </StyledNotFound>) : 
    <NftOnSellPreview meta={nftModeMeta?.meta} nft={nft} tokenid={tokenid} owner={previewNode?.owner} paytoken={previewNode?.paytoken} price={previewNode?.price} onCommitSuccess={onCommitSuccess}/>
   ))
}