import { getETH, JSBI, MIN_ETH, ChainId } from 'sdk'
import CurrencyAmount from 'sdk/entities/fractions/currencyAmount'
 
/**
 * Given some token amount, return the max that can be spent of it
 * @param currencyAmount to return max of
 */
export function maxAmountSpend(chainId : ChainId, currencyAmount?: CurrencyAmount): CurrencyAmount | undefined {
  if (!currencyAmount) return undefined

  if (currencyAmount.currency === getETH()) {
    if (JSBI.greaterThan(currencyAmount.raw, MIN_ETH)) {
      return CurrencyAmount.ether(JSBI.subtract(currencyAmount.raw, MIN_ETH))
    }

    return CurrencyAmount.ether(JSBI.BigInt(0))
  }

  return currencyAmount
}

export default maxAmountSpend
