import React from 'react'
import styled from 'styled-components'
import { getNftPayTokens, getNftMarket } from 'sdk/nft/shop'
import { getETHId, ADDRESS0 } from 'sdk'
import { useModal, Flex, Text, Button } from 'uikit'
import { useTranslation } from 'contexts/Localization'
import { getBalanceNumber } from 'utils/formatBalance'
import { useActiveWeb3React } from 'hooks'
import FoldableInput from 'components/FoldableInput'
import UnlockButton from 'components/UnlockButton'
import { useGetApiPrices } from 'state/hooks'
import { NftOnSellProps } from './types'
import NftOnSellCancelAskModal from './actions/NftOnSellCancelAskModal'
import NftOnSellUpdatePriceModal from './actions/NftOnSellUpdatePriceModal'
import NftOnSellPreviewInfoHeader from './NftOnSellPreviewInfoHeader'
import NftOnSellBuyActions from './actions/NftOnSellBuyActions'
 
const Wrapper = styled(Flex)`
  margin-top: 2px;
  width: 500px;
  max-width:500px;

  ${({ theme }) => theme.mediaQueries.md} {
    flex: 1;
    margin-top: 0px;
  }

  ${({ theme }) => theme.mediaQueries.xl} {
    flex: 2;
  }
  `
const NftSellPreviewInfoCard = styled.div`
  align-self: baseline;
  background: ${(props) => props.theme.card.background};
  border-radius: ${({ theme }) => (theme.radii.card)};
  box-shadow: 0px 1px 4px rgba(25, 19, 38, 0.15);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 24px;
  position: relative;
  text-align: center;
  width:100%;
  height:100%;
`
const NftOnSellPreviewInfo : React.FC<NftOnSellProps> = ({
  nft,
  tokenid,
  meta,
  owner,
  paytoken,
  price,
  onCommitSuccess
}) => {
  const { t } = useTranslation()

  const { account } = useActiveWeb3React()

  const isOwner = account === owner

  const market = getNftMarket()

  const shop = market.shops.find((item) => (item.nft === nft))

  const [onPresentCancelAskModal] = useModal(<NftOnSellCancelAskModal shop={shop.address} tokenid={tokenid} onSuccess={onCommitSuccess}/>)

  function handleOnCancelAsk()
  {
    onPresentCancelAskModal()
  }

  const [onPresentUpdatePriceModal] = useModal(<NftOnSellUpdatePriceModal shop={shop.address} tokenid={tokenid} paytoken={paytoken} price={price} onSuccess={onCommitSuccess}/>)

  function handleOnUpdatePrice()
  {
    onPresentUpdatePriceModal()
  }

  function createSellElement()
  {
    return isOwner ? (
     <Flex ml='12px' mb='20px' justifyContent="space-between" alignItems="center">
     {!account ? <UnlockButton /> :  <Flex><Button onClick={handleOnUpdatePrice}>
        {t('Update Ask')}
       </Button>
       
       <Button ml='20px' onClick={handleOnCancelAsk}>
        {t('Cancel Ask')}
       </Button>  
       </Flex>              
      } </Flex>)   :  (
     <Flex ml='12px' mb='20px' justifyContent="space-between" alignItems="center">
       {!account ? <UnlockButton /> : <NftOnSellBuyActions payTokenDecimals={payTokenDecimals} price={price} paytoken={paytoken} account={account} nft={nft} tokenid={tokenid} onCommitSuccess={onCommitSuccess}/> }           
     </Flex> )
  }

  const prices = useGetApiPrices()

  const { ethPrice } = prices

  const paytokens = getNftPayTokens()

  const priceTokenPrice = paytoken === ADDRESS0 ? ethPrice : prices[paytoken]

  const priceTokenSymbol = paytoken === ADDRESS0 ? getETHId() : paytokens.find((token) => token.address === paytoken).symbol

  const payTokenDecimals = paytoken === ADDRESS0 ? 18 : paytokens.find((token) => token.address === paytoken).decimals

  const priceNumber = paytoken === ADDRESS0 ? getBalanceNumber(price, 18) : getBalanceNumber(price, paytokens.find((token) => token.address === paytoken).decimals)

  const priceDollar = priceNumber * priceTokenPrice

  const isHighValue = Math.round(priceDollar * 1000) < 1

  return (
   <Wrapper>
    <NftSellPreviewInfoCard>
      <NftOnSellPreviewInfoHeader isowner={isOwner} name={meta?.name} tokenid={tokenid}/>
 
      <FoldableInput caption={t('Description')} mt="10px">
        <Text as="p" color="textSubtle" style={{ textAlign: 'left' }}>
         {meta?.desc}
        </Text>
      </FoldableInput>    

      <FoldableInput caption={t('Sell Info')} mt="10px">
        <Flex flexDirection='column' justifyContent="flex-start" >
       
        <Flex justifyContent="flex-start" alignItems="center">
         <Text color="textSubtle" fontSize="12px" bold ml='5px' mr='10px'>
           {`${priceNumber} ${priceTokenSymbol} ≈ ${priceDollar.toFixed(isHighValue ? 4 : 2)} $`}
         </Text>

         {createSellElement()}

        </Flex>
        </Flex>
      </FoldableInput>   
    </NftSellPreviewInfoCard>   
   </Wrapper>
  )
}

export default NftOnSellPreviewInfo
