/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit'
import { NotfoundNftMeta } from 'sdk'
import { getNftMarket } from 'sdk/nft/shop'
import getOGConfig from 'sdk/nft/og'
import { fetchUris, fetchMeta } from './fetchNftMeta'
import { nftMetaModuleNode, nftMetaModule, nftMetaModuleState } from '../types'
 
const { shops }= getNftMarket()

const initialState: nftMetaModuleState = { data: shops.map((shop)=>
 { 
   const item = { 
    nft: shop.nft,

    metas: new Map<string, nftMetaModuleNode>()
   } as nftMetaModule

   return item
 })}

 const ogConfig = getOGConfig()

 const shop = shops.find((s) => s.nft === ogConfig.address)

 if(!shop)
 {
   initialState.data.push({ 
    nft: ogConfig.address,

    metas: new Map<string, nftMetaModuleNode>()
   })
 }

 export const nftMetasSlice = createSlice({
  name: 'nftMetas',

  initialState,
  
  reducers: {
    setUris: (state, action) => {
      const info = action.payload

      const node = state.data.find((item) => item.nft === info.nftKey)

      if(node)
      {
        info.uris.forEach((item)=>
        {
          const sellNodeIn = node.metas.get(item.nftId)

          if(!sellNodeIn)
          {
            const sellNode = 
            {
              loading: false,
  
              uri: item.nftUri,
  
              meta: undefined
            }

            node.metas.set(item.nftId, sellNode)
          }

          else 
          {      
            sellNodeIn.uri = sellNodeIn.uri || item.nftUri     
          }
        })
      }
    },

    setLoading:  (state, action) => {
      const info = action.payload

      const node = state.data.find((item) => item.nft === info.nftKey)

      if(node)
      {
          const sellNodeIn = node.metas.get(info.nftId)

          if(sellNodeIn)
          {
            sellNodeIn.loading = true
          }

          else 
          {
            const sellNode = 
            {
              loading: true,
  
              uri: info.nftUri,
  
              meta: undefined
            }

            node.metas.set(info.nftId, sellNode)
          }
      }
    },

    setMetas: (state, action) => {
      const info = action.payload

      const node = state.data.find((item) => item.nft === info.nftKey)

      if(node)
      {
        const sellNodeIn = node.metas.get(info.nftId)

        if(sellNodeIn)
        {
          sellNodeIn.loading = false

          sellNodeIn.meta = info.nftMeta
        }
      }
    }
  }
})

// Actions
export const { setUris, setLoading, setMetas } = nftMetasSlice.actions

export default nftMetasSlice.reducer

export const fetchNftMetaAsync = (nft, tokenid, uri?) => async (dispatch) => {
  const loading = {
    nftKey: nft,

    nftId: tokenid,

    nftUri: uri
  }

  dispatch(setLoading(loading))

  let nfturi = uri

  if(!uri)
  {
     const _uris = await fetchUris(nft, [tokenid])

     nfturi = _uris[0].nftUri

     const uriinfo = 
     {
       nftKey: nft,
   
       uris : _uris
     }

     dispatch(setUris(uriinfo))
  }
 
  const _meta = await fetchMeta(nft, nfturi)

  const info = 
  {
    nftKey: nft,

    nftId: tokenid,

    nftMeta: _meta || NotfoundNftMeta,
  }

  dispatch(setMetas(info))
}
