/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit'
import { TokenType } from 'sdk'
import getTokens from 'sdk/tokens'
import { PriceState } from 'state/types'
import fetchPrices from './fetchPrices'

const tokens = getTokens()

const tokenKeys = Object.keys(tokens)

const initData = { }

tokenKeys.forEach((address)=>
{ 
  const token = tokens[address]

  if(token.type === TokenType.ETH)
  {
    initData[token.address] = undefined
  }

 else if(token.type === TokenType.USD)
  {
    initData[token.address] = 1
  }
 else {
  initData[token.address] = undefined
  } 
})

const initialState: PriceState = {
  isLoading: false,

  lastUpdated: null,

  data: initData
}

export const pricesSlice = createSlice({
  name: 'prices',

  initialState,

  reducers: {
    setPrices: (state, action) => {
      const info = action.payload

      state.data = { ...state.data, ...info}
    },
  },
})

// Actions
export const { setPrices } = pricesSlice.actions

// Thunks
export const fetchPricesAsync = () => async (dispatch) => {
  const results = await fetchPrices()
  
  dispatch(setPrices(results))
}

export default pricesSlice.reducer
