import { useCallback } from 'react'
import { useAppDispatch } from 'state'
import { useActiveWeb3React } from 'hooks'
import { getNftMarket } from 'sdk/nft/shop'
import { fetchOGUserNftsAsync, setOnSellPageQCVersion } from 'state/actions'
import { useNftShopContract } from './useContract'
import { calculateGasMargin } from '../utils'   

export default function useNftSellBuy(
  nft: string, iseth: boolean, value: string, tokenid: string, _to: string, callback: any
): [ () => Promise<void>] {
  const { account } = useActiveWeb3React()

  const dispatch = useAppDispatch()

  const market = getNftMarket()

  const shop = market.shops.find((item) => (item.nft === nft))

  const shopContract = useNftShopContract(shop.address)
  
  const onBuy = useCallback(async (): Promise<void> => {
    if (!shopContract) {
      console.error('nft shop contract is null')

      return
    }

    if(iseth)
    {
       if(_to)
       {
        const estimatedGas = await shopContract.estimateGas.buyEthTo(tokenid, _to, { value })
                                                      
        // eslint-disable-next-line consistent-return
        shopContract
          .buyEthTo(tokenid, _to, { ...{ value }, gasLimit: calculateGasMargin(estimatedGas)})
          .then(() => {
            const interval = setInterval(async () => {
              dispatch(setOnSellPageQCVersion())

              dispatch(fetchOGUserNftsAsync(account)).then(
                () =>
                {
                 clearInterval(interval)   

                 callback()     
                }
               )
            }, 6000)   
            })
            .catch((error: Error) => {
              console.error('Failed to buy', error)
              throw error
            })
       }

       else 
       {
        const estimatedGas = await shopContract.estimateGas.buyEth(tokenid, { value })
                                                      
        // eslint-disable-next-line consistent-return
        shopContract
          .buyEth(tokenid, { ...{ value }, gasLimit: calculateGasMargin(estimatedGas) })
          .then(() => {
            const interval = setInterval(async () => {
              dispatch(setOnSellPageQCVersion())

              dispatch(fetchOGUserNftsAsync(account)).then(
                () =>
                {
                 clearInterval(interval)   

                 callback()     
                }
               )
            }, 6000)   
            })
            .catch((error: Error) => {
 
              console.error('Failed to buy', error)
              throw error
            })
       }
    }

    else if(_to)
    {
        const estimatedGas = await shopContract.estimateGas.buyTo(tokenid, _to)
                                                      
        // eslint-disable-next-line consistent-return
        shopContract
          .buyTo(tokenid, _to, { gasLimit: calculateGasMargin(estimatedGas) })
          .then(() => {  
            const interval = setInterval(async () => {
              dispatch(setOnSellPageQCVersion())

              dispatch(fetchOGUserNftsAsync(account)).then(
                () =>
                {
                 clearInterval(interval)   

                 callback()     
                }
               )
            }, 6000)   
            })
            .catch((error: Error) => {
              console.error('Failed to buy', error)
              throw error
            })
      }

      else 
      {
        const estimatedGas = await shopContract.estimateGas.buy(tokenid)
                                                      
        // eslint-disable-next-line consistent-return
        shopContract
          .buy(tokenid, { gasLimit: calculateGasMargin(estimatedGas) })
          .then(() => {
            const interval = setInterval(async () => {
              
              dispatch(setOnSellPageQCVersion())

              dispatch(fetchOGUserNftsAsync(account)).then(
                () =>
                {
                 clearInterval(interval)   

                 callback()     
                }
               )
            }, 6000)   
            })
            .catch((error: Error) => {
              console.error('Failed to buy', error)
              throw error
            })
       
    }
  }, [shopContract, account, dispatch, callback, iseth, value, tokenid, _to])

  return [ onBuy ]
}

 
