import React, { useState } from 'react'
import { parseUnits } from '@ethersproject/units'
import styled from 'styled-components'
import BigNumber from 'bignumber.js'
import { getNftPayTokens } from 'sdk/nft/shop'
import { getENVChainId, getETHId, ADDRESS0 } from 'sdk'
import { Button, Modal, Flex, Text, Toggle } from 'uikit'
import { isAddress } from 'utils'
import { getBalanceNumber } from 'utils/formatBalance'
import { useGetApiPrices, useNftOnSellBuy } from 'state/hooks'
import { useTranslation } from 'contexts/Localization'
import useNftSellBuy from 'hooks/useNftSellBuy'
import { InputLabel } from 'components/Label'
 
interface NftOnSellBuyModalProps {
  account: string,

  nft: string,

  paytoken: string,

  price: BigNumber,

  tokenid: string,

  onDismiss?: () => void,

  onSuccess?: () => void
}

const StyledInput = styled.input`
  width: 240px;  
  position: relative;
  display: flex;
  padding: 5px;
  margin-right: 5px;
  align-items: center;
  white-space: nowrap;
  text-align: right;
  background: none;
  border: none;
  outline: none;
  border-radius: 12px;
  color: ${({ theme }) => theme.colors.text};
  border-style: solid;
  border: 1px solid ${({ theme }) => theme.colors.primary};
  -webkit-appearance: none;

  font-size: 12px;

  ::placeholder {
    color: ${({ theme }) => theme.colors.textDisabled};
  }
  transition: border 100ms;
  :focus {
    outline: none;
  }
`
const MessageText = styled(Text) `
  display: -webkit-box;
  overflow: hidden;
  white-space: normal !important;
  text-overflow: ellipsis;
  word-wrap: break-word;
  -webkit-box-orient: vertical;
`
const CaptionContainer = styled.div`
  min-height: 24px;
  display: flex;
  align-items: center;
`

const ContentContainer = styled.div`
  min-height: 24px;
  display: flex;
  align-items: center;
`

const NftOnSellBuyModal: React.FC<NftOnSellBuyModalProps> = ({ nft, tokenid, price, paytoken, onDismiss, onSuccess }) => {
  const [pendingTx, setPendingTx] = useState(false)

  const { t } = useTranslation()

  const nftOnSellBuy = useNftOnSellBuy(nft, tokenid)

  const { lastprice, balance, feePercent } = nftOnSellBuy || { }

  const prices = useGetApiPrices()

  const ethPrice = prices.ethPrice

  const paytokens = getNftPayTokens()

  const priceTokenPrice = paytoken === ADDRESS0 ? ethPrice : prices[paytoken]

  const priceTokenSymbol = paytoken === ADDRESS0 ? getETHId() : paytokens.find((token) => token.address === paytoken).symbol

  const payTokenDecimals =paytoken === ADDRESS0 ? 18 : paytokens.find((token) => token.address === paytoken).decimals

  const oldpriceNumber = getBalanceNumber(price, payTokenDecimals)  

  const oldpriceDollar = oldpriceNumber * priceTokenPrice

  const lastpriceNumber = getBalanceNumber(lastprice, payTokenDecimals)  

  const balanceNumber = getBalanceNumber(balance, payTokenDecimals)  

  const isoldHighValue = Math.round(oldpriceDollar * 1000) < 1

  const newPriceDollar = lastpriceNumber * priceTokenPrice

  const isnewHighValue = Math.round(newPriceDollar * 1000) < 1

  const hasNewPrice = lastpriceNumber > 0 && (lastpriceNumber !== oldpriceNumber)

  const initCommitResult = {
    step: 'step 1', 
    
    message: balanceNumber >= (lastpriceNumber > 0 ? lastpriceNumber : oldpriceNumber) ? 'waiting for commit' : t('insufficientBalance', {name: priceTokenSymbol}), 
    
    success: balanceNumber >= (lastpriceNumber > 0 ? lastpriceNumber : oldpriceNumber)
  }

  const [commitResult, setCommitResult] = useState(initCommitResult)

  const [toEnable, setToEnable] = useState(false)

  const [newPriceEnable, setNewPriceEnable] = useState(false)

  const payPrice = parseUnits((lastpriceNumber > 0 ? lastpriceNumber : oldpriceNumber).toString(), payTokenDecimals).toString()

  const [toAddressText, setToAddressText] = useState('') 

    const onToAddressChange = (e: React.ChangeEvent<HTMLInputElement>) => {
      setToAddressText(e.target.value.trim())
    }

  const onBuyCallback = () =>
  {
    setPendingTx(false)

    setCommitResult({
      success: true,

      step: "end",

      message: "success"
    })

    if(onSuccess)
    {
      onSuccess()
    }

    onDismiss()
  }

  const [ onBuy ] = useNftSellBuy(nft, paytoken ===ADDRESS0, payPrice, tokenid, toEnable ? toAddressText : undefined, onBuyCallback)

  const handleOnBuy = async () => {
      try {
        
        if(toEnable && !isAddress(toAddressText))
        {
          setCommitResult({
            success: false,
      
            step: "step 2",
      
            message: t('please input valid to address')
          }) 
        }

        else if (hasNewPrice && !newPriceEnable)
        {
          setCommitResult({
            success: false,
      
            step: "step 2",
      
            message: t('price has changed')
          }) 
        }

        else
        {
          setCommitResult({
            success: true,
      
            step: "step 1",
      
            message: t('price wait for commit')
          }) 

         setPendingTx(true)

         await onBuy()
        }
      } catch (e) {

        setPendingTx(false)

        setCommitResult({
          success: false,
    
          step: "step 2",
    
          message: e.message
        }) 
      }
  }

  return (
    <Modal title={t('Buy')} onDismiss={onDismiss} >
      <Flex maxWidth='420px' minWidth='420px' alignItems="center" flexDirection="column" justifyContent="space-between" mb="8px">
        <Text bold color="textSubtle" fontSize="18px" pr="3px">
         {t(commitResult.step)}
        </Text>
        { commitResult.success ? (<Text color="textSubtle" fontSize="14px" pr="3px" mt='10px'>
         {t(commitResult.message)}
        </Text>) :
        (<MessageText width='400px' bold color="failure" fontSize="14px" pr="3px" mt='10px'>
         {t(commitResult.message)} 
        </MessageText> )}
      </Flex>

      <Flex flexDirection="column" width='100%'>
       <Flex flexDirection="row" mt='10px' mb='5px' justifyContent="space-between">
        <CaptionContainer>
         <InputLabel text={t('Price')}/>
        </CaptionContainer>
        <ContentContainer>
           <Flex flexDirection="row" alignItems="center">
           {`${oldpriceNumber} ${priceTokenSymbol} ≈ ${oldpriceDollar.toFixed(isoldHighValue ? 4 : 2)} $`}
           </Flex>
        </ContentContainer>
       </Flex>

       <Flex flexDirection="row" mt='10px' mb='5px' justifyContent="space-between">
        <CaptionContainer>
         <InputLabel text={t('Trade Fee')}/>
        </CaptionContainer>
        <ContentContainer>
           <Flex flexDirection="row" alignItems="center">
           {`${feePercent} %`}
           </Flex>
        </ContentContainer>
       </Flex>

       { hasNewPrice &&
       (<Flex flexDirection="row" mt='10px' mb='5px' justifyContent="space-between">
         <CaptionContainer>
          <Flex ml='12px' justifyContent="center" alignItems="center">
           <Toggle scale="sm" checked={newPriceEnable} onChange={() => setNewPriceEnable((prev) => !prev)} />
           <Text ml="8px">{t('buy at new price?')}</Text>
          </Flex>
         </CaptionContainer>
        <ContentContainer>
        <Flex flexDirection="row" alignItems="center">
           {`${lastpriceNumber} ${priceTokenSymbol} ≈ ${newPriceDollar.toFixed(isnewHighValue ? 4 : 2)} $`}
           </Flex>
        </ContentContainer>
       </Flex>)}

       <Flex flexDirection="row" mt='10px' mb='5px' justifyContent="space-between">
         <CaptionContainer>
         <Flex ml='12px' justifyContent="center" alignItems="center">
          <Toggle  scale="sm" checked={toEnable} onChange={() => setToEnable((prev) => !prev)} />
          <Text ml="8px">{t('To Address')}</Text>
        </Flex>
         </CaptionContainer>
        <ContentContainer>
        <Flex flexDirection="row" alignItems="center">   
          {toEnable ? (<StyledInput value={toAddressText} placeholder={t('')} onChange={onToAddressChange} type='text'/>) : (<></>)}
         </Flex>
        </ContentContainer>
       </Flex>
       </Flex>

      <Button
        isLoading={pendingTx}
        onClick={handleOnBuy}
        mt="24px"
      >
        {pendingTx ? t('Committing') : t('Commit')}
      </Button>
    </Modal>
  )
}

export default NftOnSellBuyModal
