import React from 'react'
import { getENVChainId } from 'sdk'
import { Menu as UikitMenu } from 'uikit'
import { useActiveWeb3React } from 'hooks'
import { useTranslation, languageList } from 'contexts/Localization'
import useTheme from 'hooks/useTheme'
import useAuth from 'hooks/useAuth'
import config from './config'

const Menu = (props) => {
  const { account } = useActiveWeb3React()

  const { login, logout } = useAuth()

  const onNetworkChanged = () =>
  {
    window.location.reload()
  }

  const { isDark, toggleTheme } = useTheme()
 
  const { currentLanguage, setLanguage, t } = useTranslation()

  return (
    <UikitMenu
      account={account}
      t={t}
      chainId={getENVChainId()}
      onChangeNetwork={onNetworkChanged}
      login={login}
      logout={logout}
      isDark={isDark}
      toggleTheme={toggleTheme}
      currentLang={currentLanguage.code}
      langs={languageList}
      setLang={setLanguage}
      marketcap={undefined}
      links={config}
      profile={{
        username: undefined,
        image: undefined,
        profileLink: '/profile',
        noProfileLink: '/profile',
        showPip: undefined,
      }}
      {...props}
    />
  )
}

export default Menu
