import React, { useState } from 'react'

import {
  Modal,
  Text,
  Button,
  Flex,
  AutoRenewIcon,
} from 'uikit'

import { useTranslation } from 'contexts/Localization'

import useTheme from 'hooks/useTheme'

import useAirdropReward from 'hooks/useAirdropReward'

import useToast from 'hooks/useToast'

interface CollectModalProps {
  invitor: string
  id: number,
  rewardTokenSymbol: string,
  minDropAmount: number,
  maxDropAmount:number,
  onDismiss?: () => void
}

const CollectModal: React.FC<CollectModalProps> = ({
  invitor,
  id,
  rewardTokenSymbol,
  minDropAmount,
  maxDropAmount,
  onDismiss,
}) => {
  const { t } = useTranslation()

  const { theme } = useTheme()

  const { toastSuccess, toastError } = useToast()

  const rewardCallBack = () =>
  {
    toastSuccess(`${t('Harvested')}!`, t(`Your earnings have been sent to your wallet!`))
    
    setPendingTx(false)
    
    onDismiss()
  }

  const [ onReward ] = useAirdropReward(id, rewardCallBack, invitor)

  const [pendingTx, setPendingTx] = useState(false)

  const handleHarvestConfirm = async () => {
    setPendingTx(true)

      // harvesting
      try {
        await onReward()

      } catch (e) {
        toastError(t('Canceled'), t('Please try again and confirm the transaction.'))
        setPendingTx(false)
      }
  }

  return (
    <Modal
      title={`${rewardTokenSymbol} ${t('Harvest')}`}
      onDismiss={onDismiss}
      headerBackground={theme.colors.gradients.cardHeader}
    >
          <Flex justifyContent="space-between" mb='10px'>
             <Text ml='10px'>{t('AirdropEarnMin')}:</Text>
             <Text mr='10px'>{minDropAmount}</Text>
            </Flex>
            <Flex justifyContent="space-between" mb='10px'>
             <Text ml='10px'>{t('AirdropEarnMax')}:</Text>
             <Text mr='10px'>{maxDropAmount}</Text>
            </Flex>

      <Button
        mt="8px"
        onClick={handleHarvestConfirm}
        isLoading={pendingTx}
        endIcon={pendingTx ? <AutoRenewIcon spin color="currentColor" /> : null}
      >
        {pendingTx ? t('Confirming') : t('Confirm')}
      </Button>
      <Button variant="text" onClick={onDismiss} pb="0px">
        {t('Close window')}
      </Button>
    </Modal>
  )
}

export default CollectModal
