import { Web3Provider } from '@ethersproject/providers'
import { ConnectorNames } from 'uikit'
import { useWeb3React as useWeb3ReactCore } from '@web3-react/core'
// eslint-disable-next-line import/no-unresolved
import { Web3ReactContextInterface } from '@web3-react/core/dist/types'
import { useEffect, useState } from 'react'
import { isMobile } from 'react-device-detect'
import { NetworkContextName } from 'sdk'
import { getConnectorsByName } from '../connectors'

export function useActiveWeb3React(): Web3ReactContextInterface<Web3Provider> & { chainId?: number } {
  return useWeb3ReactCore<Web3Provider>(NetworkContextName)
}

export function useEagerConnect() {
  const { activate, active } = useActiveWeb3React() // specifically using useWeb3ReactCore because of what this hook does
 
  const [tried, setTried] = useState(false)

  const injected = getConnectorsByName(ConnectorNames.Injected)

    useEffect(() => {

    injected.isAuthorized().then((isAuthorized) => {

      if (isAuthorized ) {
        activate(injected, undefined, true).catch(() => {
          setTried(true)
        })
      } else if (isMobile && window.ethereum ) {
        activate(injected, undefined, true).catch(() => {
          setTried(true)
        })
      } else {
        if(window.ethereum?.request)
        {
          window.ethereum?.request({ method: 'eth_requestAccounts' })
        }

        setTried(true)
      }
    })
  }, [activate, injected]) // intentionally only running on mount (make sure it's only mounted once :))
  
  // if the connection worked, wait until we get confirmation of that to flip the flag
  useEffect(() => {
    if (active) {
      setTried(true)
    }
  }, [active])

  return tried
}

/**
 * Use for network and injected - logs user in
 * and out after checking what network theyre on
 */
export function useInactiveListener(suppress = false) {
  const { active, error, activate } = useActiveWeb3React() // specifically using useWeb3React because of what this hook does

  const injected = getConnectorsByName(ConnectorNames.Injected)

  useEffect(() => {
    const { ethereum } = window

    if (ethereum && ethereum.on && !active && !error && !suppress) {
      const handleChainChanged = () => {
        // eat errors
        activate(injected, undefined, true).catch((e) => {
          console.error('Failed to activate after chain changed', e)
        })
      }

      const handleAccountsChanged = (accounts: string[]) => {
      
        if (accounts.length > 0) {
          // eat errors
          activate(injected, undefined, true).catch((e) => {
            console.error('Failed to activate after accounts changed', e)
          })
        }
      }

      ethereum.on('chainChanged', handleChainChanged)
      ethereum.on('accountsChanged', handleAccountsChanged)

      return () => {
        if (ethereum.removeListener) {
          ethereum.removeListener('chainChanged', handleChainChanged)
          ethereum.removeListener('accountsChanged', handleAccountsChanged)
        }
      }
    }
    return undefined
  }, [active, error, suppress, activate, injected])
}
