import { useCallback } from 'react'
import { ADDRESS0 } from 'sdk'
import { getNftMarket } from 'sdk/nft/shop'
import { useAppDispatch } from 'state'
import { useActiveWeb3React } from 'hooks'
import { fetchOGUserNftsAsync, setOnSellPageQCVersion } from 'state/actions'
import { useNftShopContract } from './useContract'
import { calculateGasMargin } from '../utils'   

export default function useNftSellAsk(
  nft: string, tokenid: string, paytoken: string, price: string, callback: any
): [ () => Promise<void>] {
  const { account } = useActiveWeb3React()

  const dispatch = useAppDispatch()

  const market = getNftMarket()

  const shop = market.shops.find((item) => (item.nft === nft))

  const shopContract = useNftShopContract(shop.address)
  
  const onAsk = useCallback(async (): Promise<void> => {
    if (!shopContract) {
      console.error('nft shop contract is null')

      return
    }

    if(paytoken === ADDRESS0)
    {
      const estimatedGas = await shopContract.estimateGas.askEth(tokenid, price)
                                                      
      // eslint-disable-next-line consistent-return
      shopContract
             .askEth(tokenid, price, { gasLimit: calculateGasMargin(estimatedGas) })
             .then(() => {
                const interval = setInterval(async () => {
                  dispatch(setOnSellPageQCVersion()) 
                  dispatch(fetchOGUserNftsAsync(account)).then(
                    () =>
                    {
                     clearInterval(interval)   
  
                     callback()     
                    }
                   )
                }, 6000)   
        })
        .catch((error: Error) => {
          console.error('Failed to sell nft', error)
          throw error
        })
    }

    else
    {
      const estimatedGas = await shopContract.estimateGas.ask(paytoken, tokenid, price)
                                                      
      // eslint-disable-next-line consistent-return
      shopContract
             .ask(paytoken, tokenid, price, { gasLimit: calculateGasMargin(estimatedGas) })
             .then(() => {
                const interval = setInterval(async () => {
                  dispatch(setOnSellPageQCVersion()) 
                  dispatch(fetchOGUserNftsAsync(account)).then(
                    () =>
                    {
                     clearInterval(interval)   
  
                     callback()     
                    }
                   )
                }, 6000)   
        })
        .catch((error: Error) => {
          console.error('Failed to sell nft', error)
          throw error
        })
    }
    
  }, [shopContract, dispatch, account, callback, tokenid, paytoken, price])

  return [ onAsk ]
}

 
