import React from 'react'
import styled from 'styled-components'
import { NftMeta, MediaType, ogPaths } from 'sdk/nft'
import { getENVChainId } from 'sdk'
import { parseFullUrl } from 'hooks/ossClient'

interface NftMediaPreviewElementProps {
  meta: NftMeta 
}

const StyledImage = styled.img`
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  transition: opacity 1s linear;
  height: 100%;
  object-fit: contain;
  border-radius: 32px 32px 0 0;
  padding: 10px;
`
const StyledVideo = styled.video`
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  padding: 10px;
`
const NftMediaPreviewElement: React.FC<NftMediaPreviewElementProps> = ({ meta }) => {
  const { mediaType, imageurl, mkvurl, mp4url } = meta || {}

  let mediaPath = ''
  
  if(mediaType === MediaType.Image)
  {
    mediaPath = `${ogPaths.basePath}/${ogPaths.imagePath}/${getENVChainId()}/${imageurl}`
  }

  else if(mediaType === MediaType.Video)
  {
    const url = mkvurl && mkvurl.length > 0 ? mkvurl : mp4url

    mediaPath = `${ogPaths.basePath}/${ogPaths.videoPath}/${getENVChainId()}/${url}`
  }

  if (mediaType === MediaType.Video) {
      const videoComponent = (
        <StyledVideo autoPlay controls loop>
          <source src={mkvurl && mkvurl.length > 0 ? parseFullUrl(mediaPath) : ''} type="video/webm" />
          <source src={mp4url && mp4url.length > 0 ? parseFullUrl(mediaPath) : ''} type="video/mp4" />
        </StyledVideo>
      )
  
      return videoComponent
  }

  return <StyledImage src={parseFullUrl(mediaPath)} alt={meta?.name} />  
}

export default NftMediaPreviewElement
