import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import { RouteComponentProps } from 'react-router'
import { Button, Heading, Text, LogoIcon } from 'uikit'
import PageLoader from 'components/PageLoader'
import { useTranslation } from 'contexts/Localization'
import { useAppDispatch } from 'state'
import { fetchNftMetaAsync } from 'state/actions'
import { useNftMeta } from 'state/hooks'
import NftSellPreview from "./NftSellPreview"
import history from '../../routerHistory'

const StyledNotFound = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;
  height: calc(100vh - 64px);
  justify-content: center;
`
export default function NftOnSellPreviewAgent({
    match: {
      params: { nft, tokenid },
    },
  }: RouteComponentProps<{ nft: string; tokenid: string }>) {
   
   const { t } = useTranslation()

   const nftModeMeta = useNftMeta(nft, tokenid)

   const [isloading, setIsLoading] = useState(!nftModeMeta || (!nftModeMeta.meta && !nftModeMeta.loading))

   const dispatch = useAppDispatch()
 
   useEffect(() => {
     if (!nftModeMeta || (!nftModeMeta.meta && !nftModeMeta.loading))
     {  
       dispatch(fetchNftMetaAsync(nft, tokenid, nftModeMeta?.uri))
      .then(()=>
       {
         setIsLoading(false)
       })
     }
   }, [dispatch, nftModeMeta, nft, tokenid, setIsLoading])
 
   function onCommitSuccess()
   {
     history.push(`/nftog`)
   }

   return (
    isloading ? ( <PageLoader/>
     ) : (!nftModeMeta || !nftModeMeta.meta) ? ( <StyledNotFound>
      <LogoIcon width="64px" mb="8px" />
      <Heading scale="xxl">404</Heading>
      <Text mb="16px">{t('Oops, nft not found.')}</Text>
      <Button as="a" href="/nftsell" scale="sm">
        {t('Back Ordinary Gallery')}
      </Button>
    </StyledNotFound>) : 
    <NftSellPreview meta={nftModeMeta?.meta} nft={nft} tokenid={tokenid} onCommitSuccess={onCommitSuccess}/> 
   )
}