import React from 'react'
import styled from 'styled-components'
import { Flex, Text } from 'uikit'
import { useTranslation } from 'contexts/Localization'
import FoldableInput from 'components/FoldableInput'
import UnlockButton from 'components/UnlockButton'
import { useActiveWeb3React } from 'hooks'
import { NftSellProps } from './types'
import NftForSellActions from './actions/NftForSellActions'
import NftSellPreviewInfoHeader from './NftSellPreviewInfoHeader'

const Wrapper = styled(Flex)`
  margin-top: 2px;
  width: 500px;
  max-width:500px;

  ${({ theme }) => theme.mediaQueries.md} {
    flex: 1;
    margin-top: 0px;
  }

  ${({ theme }) => theme.mediaQueries.xl} {
    flex: 2;
  }
  `
const NftSellPreviewInfoCard = styled.div`
  align-self: baseline;
  background: ${(props) => props.theme.card.background};
  border-radius: ${({ theme }) => (theme.radii.card)};
  box-shadow: 0px 1px 4px rgba(25, 19, 38, 0.15);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 24px;
  position: relative;
  text-align: center;
  width:100%;
  height:100%;
`
const NftSellPreviewInfo : React.FC<NftSellProps> = ({
  nft,
  tokenid,
  meta,
  onCommitSuccess
}) => {
  const { t } = useTranslation()

  const { account } = useActiveWeb3React()

  function onSellSuccess()
  {
    if(onCommitSuccess)
    {
      onCommitSuccess()
    }
  }

  return (
   <Wrapper>
    <NftSellPreviewInfoCard>
      <NftSellPreviewInfoHeader name={meta?.name} tokenid={tokenid}/>
 
      <FoldableInput caption={t('Description')} mt="10px">
        <Text as="p" color="textSubtle" style={{ textAlign: 'left' }}>
         {meta?.desc}
        </Text>
      </FoldableInput>    
 
      <Flex justifyContent="space-between" alignItems="center" mt="10px">
      {
        !account ? <UnlockButton mt="8px" /> : <NftForSellActions account={account} nft={nft} tokenid={tokenid} onCommitSuccess={ onSellSuccess }/>
      }
      </Flex>
    </NftSellPreviewInfoCard>   
   </Wrapper>
  )
}

export default NftSellPreviewInfo
