import { tokenUriABI } from 'sdk'
import multicall from 'utils/multicall'
import getOGConfig from 'sdk/nft/og'
import { downloadBuffer } from 'hooks/ossClient'

export const fetchUris = async (nft, tokenids: string[]) => {
  const calls = tokenids.map((tokenid) =>
  {
    return {
        address: nft,
   
        name: 'tokenURI',
       
        params: [tokenid],
    }
  })

  const [uris] = await multicall(tokenUriABI, calls)

  return tokenids.map((tokenid, index)=>
  {
    return {
      nftId: tokenid,

      nftUri: uris[index]
    }
  })
}

export const fetchMeta = async(nft, uri) => {
    const ogConfig = getOGConfig()

    if(nft === ogConfig.address)
    {
      const meta = await downloadBuffer(`${ogConfig.baseurl}${uri}`)

      return meta 
    }

    return undefined
}
 

 