import React from 'react'
import styled from 'styled-components'
import { Token } from 'sdk'
import { useTranslation } from 'contexts/Localization'
import {
  Flex,
  Text,
  Skeleton 
} from 'uikit'
import Balance from 'components/Balance'

interface ExpandedFooterProps {
  rewardToken: Token,
  airdropCount: number, 
  hasAirdropCount: number, 
  hasAirdropAmount: number,
  account: string
}

const ExpandedWrapper = styled(Flex)`
  svg {
    height: 14px;
    width: 14px;
  }
`
const ExpandedFooter: React.FC<ExpandedFooterProps> = ({
  rewardToken,
  airdropCount, 
  hasAirdropCount, 
  hasAirdropAmount,
}) => {
  const { t } = useTranslation()

  const shouldShowBlockCountdown = airdropCount && (airdropCount !== hasAirdropCount)

  const blocksRemaining = airdropCount ? Math.max(airdropCount - hasAirdropCount, 0) : 0

  return (
    <ExpandedWrapper flexDirection="column">
      <Flex mb="2px" justifyContent="space-between" alignItems="center">
        <Text small>{t('Total airdrop')}:</Text>
        <Flex alignItems="flex-start">
          {hasAirdropAmount ? (
            <>
              <Balance
                fontSize="14px"
                value={hasAirdropAmount}
                decimals={0}
              />
              <Text ml="4px" fontSize="14px">
                {rewardToken.symbol}
              </Text>
            </>
          ) : (
            <Skeleton width="90px" height="21px" />
          )}
        </Flex>
      </Flex>
      {shouldShowBlockCountdown && (
        <Flex mb="2px" justifyContent="space-between" alignItems="center">
          <Text small>{t('End')}:</Text>
          <Flex alignItems="center">
            {blocksRemaining ? (
              <Balance
                color="primary"
                fontSize="14px"
                value={blocksRemaining}
                decimals={0}
              />
            ) : (
              <Skeleton width="54px" height="21px" />
            )}
            <Text ml="4px" color="primary" small>
              {t('AirdropRemainingCounts')}
            </Text>
          </Flex>
        </Flex>
      )}

    </ExpandedWrapper>
  )
}

export default React.memo(ExpandedFooter)
