/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit'
import getOGConfig from 'sdk/nft/og'
import fetchOGCreateFee from './fetchOG'
import {
  fetchOGAllowanceAndBalance,
  fetchOGUserNfts, 
} from './fetchOGUser'
import { ogState } from '../types'
import { setUris } from '../nftMetas/index'

const initialState: ogState = { data: {...getOGConfig()} }

export const ogSlice = createSlice({
  name: 'og',
  initialState,
  reducers: {
    setCreateFee: (state, action) => {
      state.data.createFee = action.payload
    },

    setUserInfo: (state, action) => {
      const userInfo = action.payload

      state.data.userallowance = userInfo.userallowance

      state.data.userbalance = userInfo.userbalance

      state.data.userNftCounts = userInfo.userNftCounts

      state.data.usernfts = userInfo.usernfts
    },

    setAB: (state, action) => {
      const userInfo = action.payload

      state.data.userallowance = userInfo.userallowance

      state.data.userbalance = userInfo.userbalance
    },

    setNfts: (state, action) => {
      const userInfo = action.payload

      state.data.userNftCounts = userInfo.userNftCounts

      state.data.usernfts = userInfo.usernfts
    },
  },
})

export const fetchOGAsync = () => async (dispatch) => {
  const createFee = await fetchOGCreateFee()

  dispatch(setCreateFee(createFee))
}

export const fetchOGUserAsync = (account) => async (dispatch) => {
  const ab = await fetchOGAllowanceAndBalance(account)

  const nfts = await fetchOGUserNfts(account)

  const ogConfig = getOGConfig()

  const uriinfo = 
  {
    nftKey: ogConfig.address,

    uris : nfts.usernfts
  }

  dispatch(setUris(uriinfo))

  const _usernfts = nfts.usernfts.map((item) =>
  {
    return item.nftId
  })

  const userInfo = 
  {
    userallowance: ab.userallowance,

    userbalance: ab.userbalance,
  
    userNftCounts: nfts.userNftCounts,
  
    usernfts: _usernfts
  }

  dispatch(setUserInfo(userInfo))
}

export const fetchOGUserABAsync = (account) => async (dispatch) => {
  const ab = await fetchOGAllowanceAndBalance(account)

  const userInfo = 
  {
    userallowance: ab.userallowance,

    userbalance: ab.userbalance,
  }

  dispatch(setAB(userInfo))
}

export const fetchOGUserNftsAsync = (account) => async (dispatch) => {
  const nfts = await fetchOGUserNfts(account)

  const ogConfig = getOGConfig()

  const uriinfo = 
  {
    nftKey: ogConfig.address,

    uris : nfts.usernfts
  }

  dispatch(setUris(uriinfo))

  const _usernfts = nfts.usernfts.map((item) =>
  {
    return item.nftId
  })

  const userInfo = 
  {
    userNftCounts: nfts.userNftCounts,
  
    usernfts: _usernfts
  }

  dispatch(setNfts(userInfo))
}

// Actions
export const { setCreateFee, setUserInfo, setAB, setNfts } = ogSlice.actions
 
export default ogSlice.reducer
