import { ht_markets, ht_payTokens} from './128'
import { bsc_markets, bsc_payTokens} from './56'
import { hsc_markets, hsc_payTokens} from './70'
import { NftMarket } from './types'
import { ChainId, getENVChainId } from '../../constants'
import { Token } from '../../types'

const nftmarkets = {
  [ChainId.BSCNET]: bsc_markets,

  [ChainId.HTNET]: ht_markets,

  [ChainId.HSCNET]: hsc_markets
}

export function getNftMarket(chainId? : ChainId) : NftMarket
{
   return chainId ? nftmarkets[chainId] : nftmarkets[getENVChainId()]
}

const nftpaytokens = {
  [ChainId.BSCNET]: bsc_payTokens,

  [ChainId.HTNET]: ht_payTokens,

  [ChainId.HSCNET]: hsc_payTokens
}

export function getNftPayTokens(chainId? : ChainId) : Token[]
{
   return chainId ? nftpaytokens[chainId] : nftpaytokens[getENVChainId()]
}